const { domReady } = wp;
const { select, dispatch, subscribe } = wp.data;

/**
 * Disable an element and prevent it from being edited.
 * @param {Node} element - element to disable
 */
const disableInput = (element) => {
  // eslint-disable-next-line no-param-reassign
  element.classList.add('read-only');
  // eslint-disable-next-line no-param-reassign
  element.readOnly = true;
  // eslint-disable-next-line no-param-reassign
  element.disabled = true;

  /**
   * Gutenberg styles disabled components using the below
   * class name. This is only ever added to the base control
   * element that is usually parent 3 levels shallow of the
   * input element. This will add/remove the required class
   * if the parent exists.
   */
  const baseControlClasses = element.closest?.('.components-base-control__field')?.classList;
  // eslint-disable-next-line no-unused-expressions
  baseControlClasses?.add?.('components-disabled');
};

/**
 * Disable all input elements on the page
 */
const disableInputs = () => {
  const inputElements = document.querySelectorAll('textarea,input,a') || [];
  inputElements.forEach((element) => {
    disableInput(element);
  });
};

/**
 * Stop post edits from being executed
 */
const stopPostEdits = () => {
  const { getSelectedBlock } = select('core/block-editor');
  const { clearSelectedBlock } = dispatch('core/block-editor');

  // prevents user from editing a block as it clears the selected blocks clientId
  if (getSelectedBlock()) {
    clearSelectedBlock();
  }

  document
    .querySelectorAll('[contenteditable]')
    .forEach((ele) => ele.removeAttribute('contenteditable'));
};

subscribe(() => {
  stopPostEdits();
  disableInputs();
});

domReady(() => {
  disableInputs();
  stopPostEdits();
});

document.addEventListener('click', (event) => {
  if (event.target.closest('[contenteditable]')) {
    event.target.blur();
  }

  stopPostEdits();
});
