import { STORE_NAME } from './settings';

const { select } = wp.data;

/**
 * Get edited post attribute.
 * @param {string} key - attribute key
 * @returns {*} attribute
 */
wp.data.select('core/editor').getEditedPostAttribute = (key) => {
  const attribute = select(STORE_NAME).getPost()?.[key];

  switch (key) {
    case 'content':
      return attribute?.raw;
    default:
      return attribute;
  }
};

const { getEntityRecord } = wp.data.select('core');
/**
 * Get entity record.
 * @param {string} kind - Entity kind
 * @param {string} type - entity type
 * @param {number} id - entity id
 * @returns {object} entity
 */
wp.data.select('core').getEntityRecord = (kind, type, id) => {
  const [leftKey, rightKey] = select(STORE_NAME).getCurrentRevision().split('-');

  if (
    kind !== 'postType' ||
    (id !== select(STORE_NAME).getPostId() &&
      id !== parseInt(rightKey, 10) &&
      id !== parseInt(leftKey, 10))
  ) {
    return getEntityRecord(kind, type, id);
  }

  return select(STORE_NAME).getRevision(id) || select(STORE_NAME).getPost();
};

const { getEditedEntityRecord } = wp.data.select('core');

/**
 * Get Edited entity record.
 * @param {string} kind - entity kind
 * @param {string} type - entity type
 * @param {number} id - entity id.
 * @returns {object} entity
 */
wp.data.select('core').getEditedEntityRecord = (kind, type, id) => {
  if (kind !== 'postType') {
    return getEditedEntityRecord(kind, type, id);
  }

  return select(STORE_NAME).getRevision(id) || select(STORE_NAME).getPost();
};

/**
 * return the current post type
 * @returns {string} post type
 */
wp.data.select('core/editor').getCurrentPostType = () => {
  const { type } = select(STORE_NAME).getPost();
  return type;
};

/**
 * set edit post to null
 */
wp.data.dispatch('core/editor').editPost = () => {};
