import { string } from '../utils';

const { registerBlockType } = wp.blocks;
const { __ } = wp.i18n;

const noContentBlockName = 'newspress/empty-content';

registerBlockType(noContentBlockName, {
  title: __('No Revisions', 'newspress'),
  category: 'common',
  supports: {
    inserter: false,
  },
  /**
   * Edit Component
   * @returns {wp.element.Component} Edit
   */
  edit: () => <div className="content">{string('editor.blocks.no_content')}</div>,
  /**
   * Save Component
   * @returns {null} save
   */
  save: () => null,
});
