const { get } = lodash;
const { settings = {} } = window.RevisionsView;

/**
 * Returns a setting based on or the default value if its not set.
 * @param {string} key - settings key
 * @param {*} defaultValue - the default value
 * @returns {*} Setting or null
 */
const getSetting = (key, defaultValue = null) => get(settings, key, defaultValue);

export default getSetting;
