import { decode } from 'he';
import CreateRevisionDiff from '../parser/CreateRevisionDiff';
import createTitleDiff from '../parser/CreateTitleDiff';
import getContentBody from '../parser/GetContentBody';
import { STORE_NAME } from '../settings';

const { cloneDeep } = lodash;
const { parse, createBlock } = wp.blocks;
const { applyFilters } = wp.hooks;
const { select: sel, dispatch: dis } = wp.data;

/**
 * Transform blocks and run through a filter.
 * @param {Array} blocks - array of blocks
 * @returns {Array} array of blocks after being processed
 */
const processBlocks = (blocks) =>
  blocks
    .map((block) => {
      if (!block) {
        return block;
      }
      const newBlock = cloneDeep(block);

      if (newBlock.name === 'core/freeform') {
        newBlock.name = 'newspress/revisions-html';
      }

      return applyFilters('newspress.revisions.processBlock', newBlock);
    })
    .filter((block) => !!block);

/**
 * Parse revision/content diffs by id.
 * @param {string} key - revision id.
 */
const parseContent = async (key) => {
  const select = sel(STORE_NAME);
  const dispatch = dis(STORE_NAME);

  // if blocks exist or its parsing then early return
  if (select.getCachedBlocks(key) || select.isParsing(key)) {
    return;
  }

  dispatch.setParsing(key, true);

  const [leftKey, rightKey] = key.split('-');

  const post = rightKey === 'post' ? select.getPost() : select.getRevision(rightKey);
  const revision = select.getRevision(leftKey);

  const bodyRevision = decode(getContentBody(revision?.content?.raw || ''));
  const bodyContent = decode(getContentBody(post?.content?.raw || ''));

  const [revisionDiff, currentDiff] = await CreateRevisionDiff(
    bodyRevision,
    bodyContent,
    revision?.newspress_status === 'autosave',
  );

  /**
   * Create Revision fill block for header.
   * @param {string} name - fill name
   * @returns {object} gutenberg block object
   */
  const revisionHeaderBlock = (name) =>
    createBlock('newspress/revision-fill', {
      name,
    });

  if (select.getView() === 'single') {
    const hideCoreTitle = applyFilters('newspress.revisions.hideCoreTitle', false);
    const latestPost = parseInt(key.split('-')[0], 10) || post.id;

    const createdTitleBlock = createBlock('newspress/revision-title', {
      title: post.id === latestPost ? post?.title?.raw : revision?.title?.raw,
    });

    const titleBlock = hideCoreTitle ? null : createdTitleBlock;

    dispatch.setBlocks(key, [
      processBlocks([
        titleBlock,
        revisionHeaderBlock('column-revision'),
        ...parse(post.id === latestPost ? bodyContent : bodyRevision),
      ]),
    ]);

    dispatch.setParsing(key, false);
    return;
  }

  const [revisionTitle, currentTitle] = await createTitleDiff(
    revision?.title?.raw,
    post?.title?.raw,
    revision?.newspress_status === 'autosave',
  );

  dispatch.setBlocks(key, [
    processBlocks([revisionTitle, revisionHeaderBlock('column-revision'), ...parse(revisionDiff)]),
    processBlocks([currentTitle, revisionHeaderBlock('column-post'), ...parse(currentDiff)]),
  ]);

  dispatch.setParsing(key, false);
};

export default parseContent;
