import string from './string';

const { applyFilters } = wp.hooks;

/**
 * Return the status string based on post data.
 * @param {string} status - post status
 * @param {object} post - post object.
 * @param {string} fallback - return value if no conditions are met.
 * @param {number} draftCount - current draft count.
 * @param {number} updatedCount - current updated count.
 * @param {number} savedCount - current saved count.
 * @returns {string} human readable status message
 */
export default ({
  status,
  post,
  revertedFrom,
  fallback = null,
  statusCount = 0,
  isTimelineFilter = false,
  transitioned = false,
}) => {
  const count = isTimelineFilter || statusCount === 0 ? '' : statusCount;

  let statusText =
    status === 'publish'
      ? `${string('editor.revision.status_messages.publish')} ${count}`
      : fallback || 'Updated';

  if (status === 'draft') {
    // If we are in the select options for the timeline filter, we don't need a count.
    statusText = `${string('editor.revision.status_messages.draft')} ${count}`;
  }

  if (status === 'reverted') {
    statusText = string('editor.revision.status_messages.reverted');
  }

  if (status === 'reverted' && revertedFrom) {
    statusText = `${string('editor.revision.status_messages.reverted')} from ${revertedFrom}`;
  }

  if (status === 'reverted' && post?.saveData?.reverted_from === 'autosave') {
    statusText = `Restored from ${string('editor.revision.status_messages.autosave')}`;
  }

  if ((status === 'publish' && !transitioned) || status === 'update') {
    statusText = `${string('editor.revision.status_messages.updated')} ${count}`;
  }

  if (status === 'future') {
    statusText = `${string('editor.revision.status_messages.future')} ${count}`;
  }

  if (status === 'saved') {
    statusText = `${string('editor.revision.status_messages.saved')} ${count}`;
  }

  // Post slug is checked incase any autosaves were saved prior to autosave integration.
  if (status === 'autosave' || (status === 'unknown' && post?.slug?.includes?.('autosave'))) {
    statusText = string('editor.revision.status_messages.autosave');
  }

  return applyFilters(
    'newspress.revisions.getStatusText',
    statusText,
    status,
    post,
    fallback,
    statusCount,
    isTimelineFilter,
  );
};
