import PropTypes from 'prop-types';
import { STORE_NAME } from '../settings';
import { restoreRevision, getStatusIcon } from '../utils';
import CompareInput from './CompareInput';

const { Button } = wp.components;
const { format } = wp.date;
const { select } = wp.data;
const { __ } = wp.i18n;

/**
 * Revision Meta Component
 * @param {object} props Component properties
 * @param {object} props.data Post data
 * @param {boolean} props.showRestore should show restore buttonm
 * @param {boolean} props.canRestore can current user restore
 * @param {boolean} props.isRestoring are we currently restoring
 * @param {string} props.statusText status text
 * @param {boolean} props.isDropdown whether input is coming from select or timeline
 * @param {boolean} props.showInput whether to show select input or not
 * @returns {wp.element.Component} RevisionMeta
 */
const RevisionMeta = ({
  data,
  showRestore = true,
  canRestore,
  isRestoring = false,
  statusText = 'Updated',
  isDropdown,
  showInput,
}) => {
  const store = select(STORE_NAME);
  const revisions = store.getRevisions();
  const restoreBtnText =
    data.newspress_status === 'autosave' ? 'Restore Autosave' : 'Restore Revision';

  return (
    <header className="revisions-editor-header">
      <div className="revisions-editor-headerTitle">
        <h1>
          {statusText}
          {!revisions[data.id] ? __(' (Most Recent)', 'newspress') : ''}
          {getStatusIcon(data.newspress_status, data)}
        </h1>
        <p className="revisions-editor-headerMeta">
          {data.authorData.name} - {format('dS M - h:ia', data.modified)}
        </p>
      </div>
      <div className="revisions-editor-headerRight">
        {showInput && (
          <CompareInput
            currentParent={data}
            revisions={revisions}
            isDropdown={isDropdown}
            showInput={showInput}
          />
        )}
        {showRestore && (
          <Button
            isPrimary
            disabled={!canRestore || isRestoring}
            onClick={() => {
              restoreRevision(data.id, data);
            }}
          >
            {isRestoring ? 'Restoring... ' : restoreBtnText}
          </Button>
        )}
      </div>
    </header>
  );
};

RevisionMeta.propTypes = {
  data: PropTypes.object,
  showRestore: PropTypes.bool,
  canRestore: PropTypes.bool,
  isRestoring: PropTypes.bool,
  statusText: PropTypes.string,
  revisions: PropTypes.object,
  isDropdown: PropTypes.bool,
  showInput: PropTypes.bool,
};

export default RevisionMeta;
