const { registerBlockType } = wp.blocks;
const { InnerBlocks } = wp.blockEditor;
const { __ } = wp.i18n;

const insBlockName = 'newspress/revision-del';

registerBlockType(insBlockName, {
  title: __('Revision Deleted', 'newspress'),
  category: 'common',
  supports: {
    inserter: false,
  },
  /**
   * Edit Component
   * @param {object} props component props
   * @param {string} props.className block classname
   * @returns {wp.element.Component} Edit
   */
  edit: ({ className }) => (
    <del className={className} data-insert-type="block">
      <InnerBlocks />
    </del>
  ),
  /**
   * Save Component
   * @returns {wp.element.Component} Save
   */
  save: () => <InnerBlocks.Content />,
});
