const { applyFilters } = wp.hooks;

/**
 * Gets an array of blocks that have no support.
 * @returns {Array} an array of non supported blocks
 */
export function blocksNoSupport() {
  return applyFilters('newspress.revisions.blocksNoSupport', []);
}

/**
 * Filter used to stop changes in the blocks content being wrapped in insertion
 * or deletion tags if in array. For example we do not want to wrap shortcode content
 * in tags as they just get outputted as text within the textarea element of the shortcode.
 * @returns {Array} array of blocks that should not be replaced
 */
export function blocksNoReplace() {
  return applyFilters('newspress.revisions.blocksNoReplace', [
    ...blocksNoSupport(),
    'shortcode',
    'embed',
    'core-embed/twitter',
    'core-embed/youtube',
    'core-embed/instagram',
  ]);
}
