import { getStatusText, transformArrayByKey, string } from '../utils/index';

/**
 * Setup Store with initial values.
 * @param {object} state - current state
 * @param {object} payload - action payload
 * @returns {object} modified state
 */
export const setup = (state, payload) => {
  const revisionsArray = payload.revisions;
  const [revisions, revisionsOrder] = transformArrayByKey(revisionsArray, 'id');

  // Construct the select options for filtering the timeline, receives 'All' by default
  const filterOptions = [{ label: string('editor.revision.status_messages.all'), value: 'all' }];

  revisionsArray.forEach((revision) => {
    const hasStatus = filterOptions.some((status) => status?.value === revision.newspress_status);

    if (!hasStatus) {
      filterOptions.push({
        label:
          revision.newspress_status === 'publish'
            ? 'Published'
            : getStatusText({
                status: revision.newspress_status,
                post: revision,
                fallback: 'Updated',
                statusCount: 0,
                isTimelineFilter: true,
              }),
        value: revision.newspress_status,
      });
    }
  });

  return {
    ...state,
    postId: payload.postId,
    editLink: payload.editLink,
    post: payload.post,
    counts: payload.counts,
    revisions,
    revisionsOrder,
    currentRevision: `${revisionsOrder[0]}-post` ?? false,
    initialising: false,
    blocks: {},
    parsing: {},
    maxPages: payload.maxPages,
    showInput: false,
    parentIndex: 0,
    revisionIndex: 0,
    stateFilter: 'all',
    filterOptions,
    status: {
      ...state.status,
      ...payload.status,
    },
  };
};

/**
 * Set parsing to true/false
 * @param {object} state - current state
 * @param {object} payload - action payload
 * @param {number} payload.key - revision id
 * @param {boolean} payload.value - parsing state.
 * @returns {object} modified state
 */
export const setParsing = (state, { key, value }) => ({
  ...state,
  parsing: {
    ...state.parsing,
    [key]: value,
  },
});

/**
 * Set revision/post blocks
 * @param {object} state - current state
 * @param {object} payload - action payload
 * @param {number} payload.key - revision id
 * @param {Array} payload.value - array of blocks
 * @returns {object} modified state
 */
export const setBlocks = (state, { key, value }) => ({
  ...state,
  blocks: {
    ...state.blocks,
    [state.view]: {
      ...state.blocks[state.view],
      [key]: value,
    },
  },
});

/**
 * Set current revision
 * @param {object} state - current state
 * @param {object} payload - contains revision ID and timeline index
 * @returns {object} modified state
 */
export const setCurrentRevision = (state, payload) => {
  let [leftKey, rightKey] = state.currentRevision.split('-');
  const indexKey = payload.index - 1 < 0 ? 'post' : state.revisionsOrder[payload.index - 1];

  if (payload.index < state.parentIndex && !payload.isDropdown && state.view !== 'single') {
    return {
      ...state,
    };
  }

  if (payload.isDropdown) {
    rightKey = state.mode === 'recent' ? 'post' : indexKey;
  } else {
    leftKey = payload.id;
  }

  return {
    ...state,
    currentRevision: `${leftKey}-${rightKey}`,
    showInput: payload.isDropdown ? state.showInput : payload.index > 0,
    parentIndex: payload.isDropdown ? payload.index : state.parentIndex,
    revisionIndex: payload.isDropdown ? state.revisionIndex : payload.index,
  };
};

/**
 * Set permissions status
 * @param {object} state - current state
 * @param {object} status - post status permissions
 * @returns {object} modified state
 */
export const setStatus = (state, status) => ({
  ...state,
  status: {
    ...state.status,
    ...status,
  },
});

/**
 * Set restoring state
 * @param {object} state - current state
 * @param {boolean} restoring - is restoring
 * @returns {object} modified state
 */
export const setRestoringStatus = (state, restoring) => ({
  ...state,
  restoring,
});

/**
 * Set post object
 * @param {object} state - current state
 * @param {object} post - new post object
 * @returns {object} modified state
 */
export const setPost = (state, post) => ({
  ...state,
  post,
});

/**
 * Set is paginating
 * @param {object} state - current state
 * @param {boolean} isPaginating - new isPaginating value
 * @returns {object} modified state
 */
export const setIsPaginating = (state, isPaginating) => ({
  ...state,
  isPaginating,
});

/**
 * Add new revisions to the stored array.
 * @param {object} state - current state
 * @param {object} payload - action payload
 * @param {number} payload.currentPage - current page
 * @param {number} payload.maxPages - maximum number of pages
 * @param {Array} payload.results - array of revisions
 * @returns {object} modified state
 */
export const paginateRevisions = (state, { currentPage, maxPages, results }) => {
  const [revisions, revisionsOrder] = transformArrayByKey(results, 'id');

  return {
    ...state,
    currentPage,
    maxPages,
    revisions: { ...state.revisions, ...revisions },
    revisionsOrder: [...state.revisionsOrder, ...revisionsOrder],
    isPaginating: false,
  };
};

/**
 * Set Block meta fields
 * @param {object} state - current state
 * @param {object} payload - action payload
 * @param {string} payload.name - block name
 * @param {Array} payload.fields - array of block fields
 * @returns {object} modified state
 */
export const setBlockMeta = (state, { name, fields }) => ({
  ...state,
  blockMeta: {
    ...state.blockMeta,
    [name]: fields,
  },
});

/**
 * Set filter state of timeline,
 * values: 'all', 'publish', or 'draft'.
 * @param {object} state - current state
 * @param {string} filter - state to filter posts by
 * @returns {object} modified state
 */
export const setFilterOptions = (state, filter) => ({
  ...state,
  stateFilter: filter,
});

/**
 * Set filter state of timeline,
 * values: 'all', 'publish', or 'draft'.
 * @param {object} state - current state
 * @param {string} filter - state to filter posts by
 * @returns {object} modified state
 */
export const filterRevisionsByState = (state, filter) => ({
  ...state,
  stateFilter: filter,
});

/**
 * Set the current view to render.
 * @param {object} state current store state.
 * @param {string} view the current view to render.
 * @returns {object} modified state.
 */
export const setView = (state, view) => {
  if (!['single', 'split'].includes(view)) {
    throw new Error('Revision view must be "single" or "split"');
  }

  return {
    ...state,
    view,
    currentRevision:
      view === 'split'
        ? `${state.revisionsOrder[0]}-post`
        : `${state.currentRevision.split('-')[0]}-post`,
    parentIndex: 0,
  };
};

export default {
  setup,
  setParsing,
  setBlocks,
  setCurrentRevision,
  setStatus,
  setRestoringStatus,
  setPost,
  setIsPaginating,
  paginateRevisions,
  setFilterOptions,
  filterRevisionsByState,
};
