import { HtmlDiffer } from './differ/lib';

/**
 * Diff two html documents and filter and split added/removed up
 * @param {string} oldString - previous string
 * @param {string} newString - new string
 * @returns {object} diffed strings split into current and past changes
 */
export default async (oldString, newString) => {
  const differ = new HtmlDiffer({
    ignoreWhitespaces: false,
    sortAttrs: false,
    sortCssClasses: false,
    sortAttrsValues: false,
  });

  const formattedDiffs = await differ.diffHtml(oldString, newString);

  return {
    currentRevision: formattedDiffs
      .map((item) => ({ ...item, added: !!item.added, removed: !!item.removed }))
      .filter((diff) => diff.added || !diff.removed),
    pastRevision: formattedDiffs
      .map((item) => ({ ...item, added: !!item.added, removed: !!item.removed }))
      .filter((diff) => !diff.added || diff.removed),
    allTokens: formattedDiffs,
  };
};
