import PropTypes from 'prop-types';
import classNames from 'classnames';
import Revision from '../components/Revision';
import RevisionHeader from '../components/RevisionMeta';
import NoticeView from './NoticeView';
import ToastView from './ToastView';
import { STORE_NAME } from '../settings';
import { getStatusText, string } from '../utils';

const { withSelect } = wp.data;

/**
 * Revisions View shows two side-by-side editors to show diffs.
 * @param {object} props - component props.
 * @param {object} props.post - current post object
 * @param {object} props.revision - current selected revision object
 * @param {Array[]} props.blocks - blocks for both the revisions editor and the post block
 * @param {boolean} props.isPostLocked - is the post current locked
 * @param {boolean} props.canRestoreRevision - can the user restore this revisions
 * @param {boolean} props.isRestoring - is the editor currently restoring
 * @param {boolean} props.isParsing - is the app currently parsing blocks
 * @param {boolean} props.isReadOnly - is read only.
 * @param {boolean} props.hasRevisions - whether post has revisions, if not, just display the current published post.
 * @param {boolean} props.showInput - whether to show select box for changing the right hand post.
 * @returns {wp.element.Component} RevisionsView Component
 */
const RevisionsView = ({
  post,
  revision,
  blocks,
  isPostLocked,
  canRestoreRevision,
  isRestoring,
  isParsing,
  isReadOnly,
  hasRevisions,
  showInput,
}) => {
  const containerClasses = classNames('revisions-editor', {
    'single-post-view': !hasRevisions,
  });

  if (!blocks && !isParsing) {
    return <NoticeView message={string('editor.messages.noBlocks')} />;
  }

  if (!blocks) {
    return <NoticeView message={string('editor.messages.loading')} />;
  }

  const [revisionBlocks, currentBlocks] = blocks || [];

  // An old autosave is one that was created before autosave integration was added (2.8.0).
  const isOldAutosave =
    !revision?.newspress_status ||
    (revision?.newspress_status === 'unknown' && revision?.slug?.includes?.('autosave'));

  return (
    <>
      {isPostLocked && <ToastView message={string('editor.messages.postLock')} />}
      <div className={containerClasses}>
        {hasRevisions && (
          <article className="revisions-editor-left">
            <RevisionHeader
              post={post}
              data={revision}
              showRestore={!isReadOnly && !isOldAutosave && (canRestoreRevision || isPostLocked)}
              canRestore={!isPostLocked}
              isRestoring={isRestoring}
              isDropdown={false}
              revision={revision}
              statusText={getStatusText({
                status: revision.newspress_status,
                post: revision,
                fallback: 'Updated',
                statusCount: revision?.newspress_count,
                transitioned: revision?.saveData?.save_transitioned || false,
                revertedFrom: getStatusText({
                  status: revision?.saveData?.reverted_from?.newspress_status,
                  post: {},
                  fallback: '',
                  statusCount: revision?.saveData?.reverted_from?.newspress_count,
                  transitioned: revision?.saveData?.reverted_from?.save_transitioned,
                }),
              })}
            />
            <Revision blocks={revisionBlocks} data={revision} />
          </article>
        )}

        <article className="revisions-editor-right">
          <RevisionHeader
            post={post}
            data={post}
            showRestore={false}
            isDropdown={true}
            showInput={showInput}
            statusText={`${getStatusText({
              status: post.newspress_status || post.status,
              post,
              fallback: 'Updated',
              statusCount: post?.newspress_count,
              transitioned: post?.saveData?.save_transitioned || false,
              revertedFrom: getStatusText({
                status: post?.saveData?.reverted_from?.newspress_status,
                post: {},
                fallback: '',
                statusCount: post?.saveData?.reverted_from?.newspress_count,
                transitioned: post?.saveData?.reverted_from?.save_transitioned,
              }),
            })}`}
          />
          <Revision blocks={currentBlocks} data={post} />
        </article>
      </div>
    </>
  );
};

RevisionsView.propTypes = {
  post: PropTypes.object.isRequired,
  revision: PropTypes.object.isRequired,
  blocks: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.object)),
    PropTypes.bool,
  ]).isRequired,
  canRestoreRevision: PropTypes.bool.isRequired,
  isPostLocked: PropTypes.bool.isRequired,
  isRestoring: PropTypes.bool.isRequired,
  isParsing: PropTypes.bool.isRequired,
  isReadOnly: PropTypes.bool.isRequired,
  hasRevisions: PropTypes.bool.isRequired,
  showInput: PropTypes.bool.isRequired,
};

const composeWithSelect = withSelect((select) => {
  const store = select(STORE_NAME);
  const currentRevision = store.getCurrentRevision();
  const [leftKey, rightKey] = currentRevision.split('-');

  return {
    post: rightKey === 'post' ? store.getPost() : store.getRevision(rightKey),
    revision: store.getRevision(leftKey),
    blocks: store.getCachedBlocks(currentRevision),
    canRestoreRevision: store.canRestoreRevision(),
    isPostLocked: store.isPostLocked(),
    isRestoring: store.isRestoring(),
    isParsing: store.isParsing(store.getCurrentRevision()),
    isReadOnly: store.readonly(),
    hasRevisions: store.getRevisionOrder().length > 0,
    showInput: store.getShowInput(),
    currentRevision,
  };
});

export default composeWithSelect(RevisionsView);
