const { registerBlockType } = wp.blocks;
const { Slot } = wp.components;
const { __ } = wp.i18n;

const blockName = 'newspress/revision-fill';
const slotName = 'newspress-revision-fill';

/** Used to display the title when using defailt WordPress title and not Multi-title block */
registerBlockType(blockName, {
  title: __('Title', 'newspress'),
  category: 'common',
  supports: {
    inserter: false,
  },
  attributes: {
    name: {
      type: 'string',
    },
  },
  /**
   * Edit Component
   * @param {object} props component props
   * @param {object} props.attributes block attributes
   * @returns {wp.element.Component} Edit
   */
  edit: ({ attributes }) => <Slot name={`${slotName}-${attributes?.name || ''}`} />,
  /**
   * Save Component
   * @returns {null} Save
   */
  save: () => null,
});
