import './styles/styles.scss';
import './store';
import './subscriptions';
import { STORE_NAME } from './settings';
import './stubs';

import App from './containers/App';
import { api } from './utils';

const { wp } = window;
const { domReady } = wp;
const { render } = wp.element;
const { registerCoreBlocks } = wp.blockLibrary;
const { dispatch, select } = wp.data;

/**
 *
 */
const pollStatus = async () => {
  const postId = select(STORE_NAME).getPostId();
  const status = await api.checkStatus(postId);
  dispatch(STORE_NAME).setStatus(status);

  let timeout = 5 * 60 * 1000; // 1 minute

  if (!status.canRestore && !status.isLocked) {
    timeout = 15 * 60 * 1000;
  }

  if (status.isLocked) {
    timeout = 10 * 1000;
  }

  setTimeout(pollStatus, timeout);
};

domReady(async () => {
  registerCoreBlocks();
  await dispatch(STORE_NAME).setup(window.RevisionsView.data);

  pollStatus();
  render(<App />, document.getElementById('revisions-view'));
});
