const PADDING = `\n\n<!-- wp:revisions/end-content -->
714710167593243925390567342553
<!-- /wp:revisions/end-content -->`;

/**
 * Strip end block away from content
 * @param {string} str - content to strip padding from
 * @returns {string} modified content
 */
export const strip = (str) => str.replace(PADDING, '');

/**
 * Append false block to the end of the
 * @param {string} str - content to apply padding to
 * @returns {string} modified content
 */
export const apply = (str) => `${str}${PADDING}`;
