/* global RevisionsView */

import PropTypes from 'prop-types';
import Provider from './Provider';

const { BlockEditorProvider, BlockList, BlockTools } = wp.blockEditor;
const { SlotFillProvider } = wp.components;
const { EntityProvider } = wp.coreData;
const { dispatch } = wp.data;
const { ShortcutProvider } = wp.keyboardShortcuts;

/**
 * Revision Editor View
 * @param {object} props component props
 * @param {Array} props.blocks editor blocks
 * @param {object} props.data revision data
 * @returns {wp.element.Component} Revision
 */
const Revision = ({ blocks = [], data }) => {
  // Setup editor can return a uncaught promise. Wrap this call in a try / catch, to ensure that the revision doesn't error out.
  try {
    dispatch('core/editor').setupEditor(data);
  } catch (e) {
    // eslint-disable-next-line
    console.error(e);
  }

  return (
    <div className="revision-article-content">
      <Provider provider={ShortcutProvider}>
        <Provider
          provider={EntityProvider}
          id={data.id}
          kind="postType"
          type={RevisionsView.data.post.type}
        >
          <Provider provider={SlotFillProvider}>
            <Provider
              provider={BlockEditorProvider}
              value={blocks}
              settings={window.RevisionsView.editor_settings}
            >
              <BlockTools>
                <div className="editor-styles-wrapper">
                  <div className="test">
                    <BlockList renderAppender={() => null} />
                  </div>
                </div>
              </BlockTools>
            </Provider>
          </Provider>
        </Provider>
      </Provider>
    </div>
  );
};

Revision.propTypes = {
  blocks: PropTypes.arrayOf(PropTypes.object),
  data: PropTypes.object,
};

export default Revision;
