/**
 * Checks if a block id exists within an array of blocks (and child blocks)
 * @param {Array} blocks - array of blocks to check
 * @param {string} clientId - block id to find
 * @returns {boolean} true/false if the block is found in the array
 */
const blockExists = (blocks, clientId) => {
  for (let i = 0; i < blocks.length; i += 1) {
    const block = blocks[i];

    if (block.clientId === clientId) {
      return true;
    }

    if (block.innerBlocks.length > 0 && blockExists(block.innerBlocks, clientId)) {
      return true;
    }
  }

  return false;
};

export default blockExists;
