const { registerBlockType } = wp.blocks;
const { __ } = wp.i18n;

const columnBlockName = 'newspress/revisions-html';

registerBlockType(columnBlockName, {
  title: __('Revision HTML', 'newspress'),
  category: 'layout',
  supports: {
    inserter: false,
  },
  attributes: {
    content: {
      type: 'string',
    },
  },
  /**
   * Edit Component
   * @param {object} props component properties
   * @param {object} props.attributes block attributes
   * @returns {wp.element.Component} Edit
   */
  edit: ({ attributes }) => (
    <div
      className={`wp-newspress-revision-column`}
      dangerouslySetInnerHTML={{ __html: attributes.content }}
    />
  ),
  /**
   * Save Component
   * @returns {null} Save
   */
  save: () => null,
});
