import RevisionParser from './RevisionParser';
import * as padding from '../utils/pad';

const options = {
  stripHTML: [
    {
      name: 'x-text-format',
      removeText: true,
      attributes: {
        class: 'delete-marker',
      },
    },
    {
      name: 'x-text-format',
      attributes: {
        class: 'corrections-marker',
      },
    },
    {
      name: 'x-text-format',
      attributes: {
        class: 'legal-marker',
      },
    },
  ],
};

/**
 * Get diffed content
 * @param {string} bodyRevision - revision content
 * @param {string} bodyContent - current content
 * @param {boolean} reverseDiffTags - whether diff needs to be switched between ins and del
 * @returns {Array} content
 */
export default async (bodyRevision, bodyContent, reverseDiffTags = false) => {
  /**
   * Add padding blocks to prevent any selfclosing
   * blocks at end of the content from being removed
   * by the parsing process. Padding blocks are removed
   * later once padding is complete.
   */
  const revisionDiff = new RevisionParser(padding.apply(bodyRevision), padding.apply(bodyContent), {
    ...options,
    reverseDiffTags,
  });

  // If no revisions, return standard content strings.
  if (!bodyRevision) {
    return [bodyRevision, bodyContent];
  }

  await revisionDiff.applyDiffsToParsed();
  const updatedContentString = revisionDiff.getCurrent().toString();
  const updatedRevisionString = revisionDiff.getPrevious().toString();

  return [padding.strip(updatedRevisionString), padding.strip(updatedContentString)];
};
