import classNames from 'classnames';
import PropTypes from 'prop-types';
import { getStatusIcon, getStatusText } from '../utils';

const { __ } = wp.i18n;
const { Button } = wp.components;
const { format } = wp.date;

/**
 * TimelineItem Component
 * @param {object} props component props
 * @param {Function} props.onClick onClick handler
 * @param {string} props.status revision status
 * @param {string} props.date revision date
 * @param {string} props.name user name
 * @param {boolean} props.isSelected currently selected
 * @param {object} props.additional additional data
 * @param {boolean} props.isMostRecent is most recent entry
 * @param {number} props.statusCount number of available posts with current props.status
 * @param {object} props.post entry object.
 * @param {number} props.timelineIndex timeline index pos of left hand side post
 * @param {number} props.parentIndex timeline index pos of right hand side post
 * @param props.indexes
 * @param props.counts
 * @param {boolean} props.isParent whether the timeline entry is the past revision or the future revision.
 * @returns {wp.components.Component} TimelineItem
 */
export const TimelineItem = ({
  onClick,
  status,
  date,
  post,
  name,
  isSelected,
  additional,
  isMostRecent,
  statusCount,
  timelineIndex,
  parentIndex,
  isParent,
}) => {
  const classes = classNames('revisionsTimeline-listItem', {
    isSelected,
    'parent-entry': isParent || isMostRecent,
  });

  const revisionStatus = getStatusText({
    status,
    post,
    fallback: __('Updated', 'newspress'),
    statusCount,
    transitioned: post?.saveData?.save_transitioned || false,
    revertedFrom: getStatusText({
      status: post?.saveData?.reverted_from?.newspress_status,
      post: {},
      fallback: '',
      statusCount: post?.saveData?.reverted_from?.newspress_count,
      transitioned: post?.saveData?.reverted_from?.save_transitioned,
    }),
  });

  let savedBy = name;

  if (additional?.savedBy) {
    savedBy = additional.savedBy.name;
  }

  return (
    <li className={classes} data-revision-id={post.id}>
      <div className="listItem-innerWrapper">
        <Button
          disabled={timelineIndex < parentIndex - 1 || (isMostRecent && parentIndex > 0)}
          onClick={onClick}
        >
          <span className="listItem-title">
            {revisionStatus}
            {isMostRecent && ' (Most Recent)'}
            {getStatusIcon(status, post)}
          </span>
          <span className="listItem-nameDate">
            {savedBy} <br />
            {format('dS M - h:ia', date)}
          </span>
        </Button>
      </div>
    </li>
  );
};

TimelineItem.propTypes = {
  onClick: PropTypes.func,
  status: PropTypes.string,
  date: PropTypes.string,
  post: PropTypes.object,
  name: PropTypes.string,
  isSelected: PropTypes.bool,
  additional: PropTypes.object,
  isMostRecent: PropTypes.bool,
  statusCount: PropTypes.number,
  timelineIndex: PropTypes.number,
  parentIndex: PropTypes.number,
  isParent: PropTypes.bool,
};

export default TimelineItem;
