import PropTypes from 'prop-types';

/**
 * Wrap children in an element if it exists.
 * @param {object} props component props
 * @param {object} props.children component children
 * @param {wp.element.Component} props.provider Provider to maybe wrap around children
 * @returns {wp.element.Component} wrapped children
 */
const Provider = ({ provider: MaybeProvider, children, ...props }) => {
  if (!MaybeProvider) {
    return children;
  }

  return <MaybeProvider {...props}>{children}</MaybeProvider>;
};

Provider.propTypes = {
  provider: PropTypes.elementType,
  children: PropTypes.object,
};

export default Provider;
