import PropTypes from 'prop-types';

const { registerBlockType } = wp.blocks;
const { InnerBlocks } = wp.blockEditor;
const { useSelect } = wp.data;
const { useEffect, useState } = wp.element;
const { __ } = wp.i18n;

const blockName = 'newspress/revision-variable';

/**
 * @typedef CustomTag
 * @param {object} props The component props.
 * @param {string} props.clientId The clientId of the block.
 * @param {string} props.className The class name for the block.
 * @returns {CustomTag} The CustomTag component.
 */
const Component = ({ clientId, className }) => {
  const childBlocks = useSelect((select) => select('core/block-editor').getBlocks(clientId));
  const [hasChanges, setChanges] = useState(false);
  const [CustomTag, setTag] = useState('div');

  useEffect(() => {
    const { attributes: { hasRevisions = false, isRevision = false } = false } =
      childBlocks?.[0] || {};

    if (hasRevisions) {
      setChanges(hasRevisions);
      setTag(isRevision ? 'del' : 'ins');
    }
  }, [childBlocks]);

  return (
    <CustomTag
      className={className}
      data-changes={hasChanges}
      data-insert-type="block"
      data-change-type={CustomTag}
    >
      <InnerBlocks />
    </CustomTag>
  );
};

Component.propTypes = {
  clientId: PropTypes.string,
  className: PropTypes.string,
};

registerBlockType(blockName, {
  title: __('Revision', 'newspress'),
  category: 'common',
  supports: {
    inserter: false,
  },
  /**
   * Edit Component
   * @param {object} props component props
   * @param {string} props.className block className
   * @returns {wp.element.Component} Edit
   */
  edit: Component,
  /**
   * Save Component
   * @returns {wp.element.Component} Save
   */
  save: () => <InnerBlocks.Content />,
});
