import * as effects from './effects';

const defaultState = {
  initialising: true,
  postId: -1,
  mode: 'compare',
  post: false,
  editLink: false,
  draftCount: 0,
  selected: -1,
  counts: {},
  currentRevision: -1,
  currentPage: 1,
  isPaginating: false,
  revisions: {},
  revisionsOrder: [],
  blocks: {},
  parsing: {},
  status: {
    canRestore: false,
    isLocked: true,
  },
  restoring: false,
  blockMeta: {},
  showInput: false,
  revisionIndex: 1,
  parentIndex: 0,
  stateFilter: 'all',
  filterOptions: [],
};

/**
 * Store reducer
 * @param {object} state - current state
 * @param {object} action - store action
 * @param {string} action.type - Action type
 * @param {*} action.payload - Action payload
 * @returns {object} new state
 */
const reducer = (state = defaultState, { type, payload }) => {
  const newState = { ...state };

  switch (type) {
    case 'SETUP':
      return effects.setup(newState, payload);
    case 'SET_PARSING':
      return effects.setParsing(newState, payload);
    case 'SET_BLOCKS':
      return effects.setBlocks(newState, payload);
    case 'SET_CURRENT_REVISION':
      return effects.setCurrentRevision(newState, payload);
    case 'SET_STATUS':
      return effects.setStatus(newState, payload);
    case 'SET_RESTORING_STATUS':
      return effects.setRestoringStatus(newState, payload);
    case 'SET_POST':
      return effects.setPost(newState, payload);
    case 'SET_IS_PAGINATING':
      return effects.setIsPaginating(newState, payload);
    case 'PAGINATE_REVISIONS':
      return effects.paginateRevisions(newState, payload);
    case 'SET_BLOCK_META':
      return effects.setBlockMeta(newState, payload);
    case 'FILTER_REVISIONS_BY_STATE':
      return effects.filterRevisionsByState(newState, payload);
    case 'SET_REVISIONS_VIEW':
      return effects.setView(newState, payload);
    default:
      return newState;
  }
};

export default reducer;
