const { useEffect, useRef } = wp.element;

/**
 * React hook to store the previous values from the render, similar to prevProps.
 * @param {*} value - the current value to be stored
 * @returns {*} the previous value passed to the hook
 */
export default function usePrevious(value) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}
