/**
 * Parse diff blocks and piece them together preserving html elements
 * @param {Array} diffBlocks - unprocessed diffs
 * @returns {Array} diffs that maintains html tags and attributes
 */
export default (diffBlocks) => {
  // @TODO: Look into simplifying this as there is a high level of complexity that may be possible to simplify
  const startOfStringRegex = new RegExp(`\(<[^!]*?\>)`, 'g'); // eslint-disable-line
  const startsWithUncompleteBlockRegex = new RegExp(/(^[^<]*?>)/);
  const endsWithUncompleteBlockRegex = new RegExp(/(<[^>]*$)/);
  const elemTree = [];
  let incompleteTags = 0;

  // Self closing tags that do not have to end in />
  const selfClosingTags = [
    'meta',
    'img',
    'input',
    'hr',
    'br',
    'track',
    'source',
    'link',
    'area',
    'base',
    'col',
    'wbr',
    'embed',
  ];

  const selfClosingTagsRegex = new RegExp(`^<${selfClosingTags.join('|')}`);

  return diffBlocks
    .reduce((carry, item) => {
      if (item.block.selfClosing) {
        return [...carry, item];
      }

      const newCarry = carry;

      let items = item.value.split(startOfStringRegex).filter((v) => !!v);

      const startComparingFromArr = [];

      items = items.map((value, key) => {
        const isTag = !!value.match(/<.*?>/);

        if (value === '<br>') {
          elemTree.push('<br/>');
        }

        if (
          isTag &&
          (item.added || elemTree.length > 0) &&
          !value.startsWith('</') &&
          !value.endsWith('/>')
        ) {
          if (!selfClosingTagsRegex.test(value)) {
            elemTree.push(value);
          }
        }

        if (
          // if the current value is a tag
          isTag &&
          // if the item is added
          item.added &&
          // if the current item isn't a closing tag
          !value.startsWith('</') &&
          // if we arent the first in the list.
          key > 0 &&
          // if the previous item is a closing tag
          items[key - 1].startsWith('</') &&
          // if the previous item is the same as the current opening tag
          items[key - 1].replace('/', '') === value.replace(/(<.*?)(?:\s.*?)(>)/, '$1$2') &&
          // if we currently have an index in the elemTree array
          elemTree.length > 0
        ) {
          startComparingFromArr.push(key);
          elemTree.pop();
        }

        if ((value.startsWith('</') || value.endsWith('</')) && elemTree.length > 0) {
          elemTree.pop();
        }

        return {
          ...item,
          value,
          added: isTag ? false : elemTree.length > 0 || item.added,
          isTag,
        };
      });

      // if we have a potential duplicate content showing in the diffs.
      if (startComparingFromArr.length > 0) {
        // previous Item
        const previousItem = newCarry[newCarry.length - 1];
        // for each of these points
        startComparingFromArr.forEach((startComparingFrom) => {
          // build up a string from the current items so we can start comparing.
          const string = items
            .filter((i, key) => key > startComparingFrom && !i.isTag)
            .map(({ value }) => value)
            .join('');
          if (
            // string isnt ''
            string &&
            // if the string starts with previousItem
            string.startsWith(previousItem.value) &&
            // ignore if previous item is a tag.
            !previousItem?.isTag &&
            // if the previousItem could be the start of an unfinished tag bail
            !endsWithUncompleteBlockRegex.test(previousItem.value)
          ) {
            newCarry[newCarry.length - 1].added = true;
            for (let i = startComparingFrom + 1; i < items.length; i += 1) {
              items[i].added = false;
            }
          }
        });
      }

      return [...newCarry, ...items];
    }, [])
    .reduce((carry, item) => {
      const emptyAdditions = [];

      if (
        startsWithUncompleteBlockRegex.test(item.value) ||
        endsWithUncompleteBlockRegex.test(item.value)
      ) {
        const results = item.value
          .split(startsWithUncompleteBlockRegex)
          .filter((v) => !!v)
          .reduce(
            (curr, string) => [
              ...curr,
              ...string.split(endsWithUncompleteBlockRegex).filter((v) => !!v),
            ],
            [],
          )
          .reduce((curr, string) => {
            if (
              startsWithUncompleteBlockRegex.test(string) ||
              endsWithUncompleteBlockRegex.test(string)
            ) {
              if (startsWithUncompleteBlockRegex.test(string)) {
                incompleteTags -= 1;
                incompleteTags = Math.max(incompleteTags, 0);
              }

              if (endsWithUncompleteBlockRegex.test(string)) {
                incompleteTags += 1;
                incompleteTags = Math.min(incompleteTags, 1);
              }

              return [
                ...curr,
                {
                  ...item,
                  value: string,
                  added: false,
                  removed: false,
                  isTag: item.isTag || true,
                },
              ];
            }

            return [
              ...curr,
              {
                ...item,
                value: string,
                added: incompleteTags > 0 ? false : item.added,
                removed: incompleteTags > 0 ? false : item.removed,
                isTag: item.isTag || incompleteTags > 0,
              },
            ];
          }, []);

        if (item.added && incompleteTags > 0) {
          emptyAdditions.push({
            ...item,
            added: false,
            removed: true,
            isTag: item.isTag || true,
          });
        }

        return [...carry, ...results, ...emptyAdditions];
      }

      if (item.added && incompleteTags > 0) {
        emptyAdditions.push({
          ...item,
          added: false,
          removed: true,
          isTag: item.isTag || true,
        });
      }

      return [
        ...carry,
        {
          ...item,
          added: incompleteTags > 0 ? false : item.added,
          removed: incompleteTags > 0 ? false : item.removed,
          isTag: item.isTag || incompleteTags > 0,
        },
        ...emptyAdditions,
      ];
    }, [])
    .filter(({ value, block }) => !!value || block?.selfClosing);
};
