import allowedHTMLTagsRegExp from './getAllowedTags';

const NEWLINE_BLOCK = '\n\n\n\n';
const REGEX_BLOCK_WITH_CONTENT =
  /(?:(?:<!-- wp:((?:(?!revision-).)+?)(?:\s({.+?}))?\s(\/)-->)|(?:<!-- wp:((?:(?!revision-).)+?)(?:\s({.+?}))?\s-->)(?:\n(.[\s\S]+?)\n)<!-- \/wp:\4 -->)/;

/**
 * Is the string a block without content
 * @param {string} content - content to check
 * @returns {boolean} is the string a templated block
 */
const isTemplatedBlock = (content) => {
  const templatedBlockRegex = new RegExp(/<!-- wp:(.+?)( {.*?})? (\/)?-->/, 'g');
  const matches = content.match(templatedBlockRegex);

  return matches.length > 1;
};

/**
 * Collapse template blocks
 * @param {string} html - html string
 * @returns {string} cleaned blocks
 */
const collapseTemplatedBlocks = (html) => {
  const singleBlockRegex = new RegExp(REGEX_BLOCK_WITH_CONTENT, 'g');
  const blockTagRegex = new RegExp(/<!-- (?:\/)?wp:.+?(?: {.*?})? (\/)?-->/, 'g');
  const newLineRegex = new RegExp(/\n{1,}/, 'g');

  const contentBlocks = html.match(singleBlockRegex);

  const cleanedBlocks = [];

  if (!contentBlocks) {
    return '';
  }

  contentBlocks.forEach((block) => {
    singleBlockRegex.lastIndex = 0;

    if (!isTemplatedBlock(block)) {
      cleanedBlocks.push(block);
      return;
    }

    const matches = singleBlockRegex.exec(block);
    const tags = block.split(matches[6]);

    const deblocked = block.replace(blockTagRegex, '');
    const cleanContent = deblocked.replace(newLineRegex, '\n');
    tags.splice(1, 0, cleanContent);

    cleanedBlocks.push(tags.join(''));
  });

  return cleanedBlocks.join('\n\n');
};

/**
 * Replace html with encoding
 * @param {string} html - html content to convert
 * @returns {string} converted content
 */
export default (html = '') => {
  const blockedHTML = collapseTemplatedBlocks(html);

  const allowedTagsRegExp = allowedHTMLTagsRegExp();
  let tags = blockedHTML.replace(allowedTagsRegExp, '{x-html{$1}x-html}');
  tags = tags.replace(/&(.*?);/g, '{x-html-ent{$1}x-html-ent}');

  const doc = new DOMParser().parseFromString(tags, 'text/html');
  let value = doc.body.textContent || '';

  value = value.replace(allowedTagsRegExp, '<$1>');
  value = value.replace(/{x-html-ent{(.*?)}x-html-ent}/g, '&$1;');

  // Test for excessive amounts of consecutive new lines and
  // replace them with the defined NEWLINE_BLOCK.
  const regex = new RegExp(/\n{5,}/, 'g');

  if (regex.test(value)) {
    /* istanbul ignore next */
    value = value.replace(regex, NEWLINE_BLOCK);
  }

  return value;
};
