import { STORE_NAME } from '../settings';
import api from './api';

const { dispatch } = wp.data;

/**
 * Restore a post to a revision by revisionId
 * @param {string|number} revisionId - The revision ID to restore.
 */
const restoreRevision = async (revisionId) => {
  dispatch(STORE_NAME).setRestoring(true);
  const response = await api.restoreRevision(revisionId);
  const data = await api.getPostData(response.data.postId);

  dispatch(STORE_NAME).setup(data);
  dispatch(STORE_NAME).setRestoring(false);
};

export default restoreRevision;
