const { registerBlockType } = wp.blocks;
const { InnerBlocks } = wp.blockEditor;
const { __ } = wp.i18n;

const columnBlockName = 'newspress/revision-column';

registerBlockType(columnBlockName, {
  title: __('Revision Column', 'newspress'),
  category: 'layout',
  supports: {
    inserter: false,
  },
  attributes: {
    columnNumber: {
      default: 1,
      type: 'number',
    },
    columnName: {
      default: 'column',
      type: 'string',
    },
  },
  /**
   * Edit Component
   * @param {object} props component props
   * @param {object} props.attributes block attributes
   * @returns {wp.element.Component} Edit
   */
  edit: ({ attributes }) => (
    <div
      className={`wp-newspress-revision-column column-${attributes.columnNumber} column-${attributes.columnName}`}
    >
      <InnerBlocks />
    </div>
  ),
  /**
   * Save Component
   * @returns {wp.element.Component} Save
   */
  save: () => (
    <div className="wp-newspress-revision-column">
      <InnerBlocks.Content />
    </div>
  ),
});
