import PropTypes from 'prop-types';
import classNames from 'classnames';
import { STORE_NAME } from '../settings';
import { getStatusText } from '../utils';

const { __ } = wp.i18n;
const { useState, useRef } = wp.element;
const { Popover, Dashicon, Button } = wp.components;
const { withSelect, withDispatch } = wp.data;
const { compose } = wp.compose;
const { format } = wp.date;

/**
 * CompareInput component
 * @param {object} props - block props
 * @param {object} props.post - current post compared to (right hand post)
 * @param {object} props.revisions - list of revisions
 * @param {object} props.popover - popover parameters
 * @param {Function} props.setCurrentRevision - sets current revision
 * @param {boolean} props.isDropdown - whether the set revision is from the select or the timeline
 * @param {boolean} props.revisionIndex - revision position on the timeline
 * @param {Array} props.revisionsOrder - order of revisions to display
 * @param {object} props.currentParent - current post selected on the right post diff
 * @param {number} props.parentIndex - current index of right hand side post to compare.
 * @returns {wp.element.Component} CompareInput componenet
 */
const CompareInput = ({
  post,
  revisions = {},
  popover = { position: 'bottom left' },
  setCurrentRevision,
  isDropdown,
  revisionIndex,
  revisionsOrder,
  currentParent,
  parentIndex,
}) => {
  const [isOpen, setOpen] = useState(false);
  const selectElement = useRef(null);

  const postStatus = getStatusText({
    status: post.newspress_status || post.status,
    post,
    fallback: __('Updated', 'newspress'),
    statusCount: post.newspress_count,
    transitioned: post?.saveData?.save_transitioned || false,
    revertedFrom: getStatusText({
      status: post?.saveData?.reverted_from?.newspress_status,
      post: {},
      fallback: '',
      statusCount: post?.saveData?.reverted_from?.newspress_count,
      transitioned: post?.saveData?.reverted_from?.save_transitioned,
    }),
  });

  return (
    <>
      <div ref={selectElement} className="dropdown" onClick={() => setOpen(!isOpen)}>
        <span>
          {getStatusText({
            status: currentParent.newspress_status,
            currentParent,
            fallback: __('Updated', 'newspress'),
            statusCount: currentParent.newspress_count,
            transitioned: currentParent?.saveData?.save_transitioned || false,
          })}
          <em className="dropdown-date"> - {format('dS M - h:ia', ` ${currentParent.date}`)}</em>
        </span>
        <Dashicon icon="arrow-down-alt2" />
      </div>
      {isOpen && (
        <Popover
          onFocusOutside={() => setOpen(false)}
          anchorRef={selectElement.current}
          className="dropdownPopover"
          {...popover}
        >
          <ul className="dropdownOptions">
            <li key={post.id} className="dropdown-option">
              <Button
                className={`dropdown-button ${parentIndex === 0 && 'is-selected'}`}
                onClick={setCurrentRevision(post.id, 0, isDropdown)}
              >
                <span>
                  {postStatus} -{' '}
                  <em className="dropdown-date">{format('dS M - h:ia', ` ${post.date}`)}</em>
                </span>
              </Button>
            </li>
            {revisionsOrder.map((ids, index) => {
              const revision = revisions[ids];

              // eslint-disable-next-line camelcase
              const { saveData, status, newspress_status, date, id, newspress_count } = revision;

              if (revisionIndex <= index || index === revisionsOrder.length - 1) {
                return null;
              }

              const statusText = getStatusText({
                // eslint-disable-next-line camelcase
                status: newspress_status || status,
                revision,
                fallback: __('Updated', 'newspress'),
                // eslint-disable-next-line camelcase
                statusCount: newspress_count,
                transitioned: saveData?.save_transitioned || false,
                revertedFrom: getStatusText({
                  status: saveData?.reverted_from?.newspress_status,
                  post: {},
                  fallback: '',
                  statusCount: saveData?.reverted_from?.newspress_count,
                  transitioned: saveData?.reverted_from?.save_transitioned,
                }),
              });

              const buttonClasses = classNames('dropdown-button', {
                'is-selected': index === parentIndex - 1,
              });

              return (
                <li key={revision.id} className="dropdown-option">
                  <Button
                    className={buttonClasses}
                    onClick={setCurrentRevision(id, index + 1, isDropdown)}
                  >
                    <span>
                      {statusText} -{' '}
                      <em className="dropdown-date">{format('dS M - h:ia', ` ${date}`)}</em>
                    </span>
                    <span> </span>
                  </Button>
                </li>
              );
            })}
          </ul>
        </Popover>
      )}
    </>
  );
};

CompareInput.propTypes = {
  data: PropTypes.object,
  post: PropTypes.object,
  revisions: PropTypes.object,
  popover: PropTypes.object,
  setCurrentRevision: PropTypes.func,
  isDropdown: PropTypes.bool,
  revisionIndex: PropTypes.number,
  revisionsOrder: PropTypes.array,
  currentParent: PropTypes.object,
  parentIndex: PropTypes.number,
};

const composeWithSelect = withSelect((select) => {
  const store = select(STORE_NAME);

  return {
    currentRevision: store.getCurrentRevision(),
    revisions: store.getRevisions(),
    revisionsOrder: store.getRevisionOrder(),
    parentIndex: store.getParentIndex(),
    revisionIndex: store.getRevisionIndex(),
    post: store.getPost(true),
  };
});

const composeWithDispatch = withDispatch((dispatch) => {
  const store = dispatch(STORE_NAME);

  return {
    /**
     * Set current Revision
     * @param {number} id revision id
     * @param {number} index index of revision timeline item
     * @param {boolean} isDropdown whether it is timeline onclick or input onclick
     * @returns {Function} update current revision
     */
    setCurrentRevision: (id, index, isDropdown) => () =>
      store.setCurrentRevision({ id, index, isDropdown }),
  };
});

export default compose(composeWithSelect, composeWithDispatch)(CompareInput);
