import { STORE_NAME } from '../settings';

const { select } = wp.data;

/**
 * Compare block meta fields from revision to post
 * @param {string} blockName - block name
 * @returns {boolean} is different
 */
export const compareMeta = (blockName) => {
  const blockMetaFields = select(STORE_NAME).getBlockMetaFields(blockName);

  let isDiff = false;

  if (!blockMetaFields) {
    return isDiff;
  }

  const { meta: revisionMeta = {} } = select(STORE_NAME).getCurrentRevisionObject();
  const { meta: postMeta = {} } = select(STORE_NAME).getPost();

  blockMetaFields.forEach((field) => {
    if (revisionMeta?.[field] !== postMeta?.[field]) {
      isDiff = true;
    }
  });

  return isDiff;
};

export default compareMeta;
