/* eslint-disable react/no-danger */
const { registerBlockType } = wp.blocks;
const { __ } = wp.i18n;

const titleBlockName = 'newspress/revision-title';

registerBlockType(titleBlockName, {
  title: __('Revision Title', 'newspress'),
  category: 'common',
  supports: {
    inserter: false,
  },
  attributes: {
    title: {
      default: '',
      source: 'html',
      type: 'string',
    },
  },
  /**
   * Title edit block
   * @param {object} props component props
   * @param {object} props.attributes block attributes
   * @param {string} props.name block name
   * @returns {wp.element.Component} Edit
   */
  edit: ({ attributes, name }) => (
    <div className="editor-styles-wrapper">
      <div className="editor-post-title__block">
        <h2
          className="editor-post-title__input revisions-post-title"
          data-block={name}
          dangerouslySetInnerHTML={{ __html: attributes.title }}
        />
      </div>
    </div>
  ),
  /**
   * Save Component
   * @returns {null} nothing
   */
  save: () => null,
});
