/* global RevisionsView */
import getSetting from './getSetting';

const { apiFetch } = wp;

/**
 * prepend revisions api namespace to route.
 * @param {string} route - route to be appended.
 * @returns {string} full namespaced route.
 */
const path = (route) => `revisions/v1/${route}`;

/**
 * check post lock/edit status.
 * @param {number} postId - post id to check
 * @returns {object} api response
 */
const checkStatus = (postId) =>
  apiFetch({
    path: path(`check-status/${postId}`),
  });

/**
 * Restore a post to a specific revision
 * @param {number} revisionId - revision id.
 * @returns {object} api response
 */
const restoreRevision = (revisionId) =>
  apiFetch({
    path: path(`restore/${revisionId}`),
    method: 'POST',
  });

/**
 * Get post data.
 * @param {number} postId - post id.
 * @returns {object} api response
 */
const getPostData = (postId) =>
  apiFetch({
    path: path(`post/${postId}`),
  });

/**
 * get post's revisions.
 * @param {number} postId - post id.
 * @param {number} page - page to get.
 * @returns {object} api response
 */
const getRevisions = (postId, page = 1) =>
  apiFetch({
    path: `${
      RevisionsView.data.restUrl
    }/${postId}/revisions?page=${page}&context=edit&per_page=${getSetting(
      'revisions_per_page',
      50,
    )}&exclude=${RevisionsView.data.autosaves.join(',')}`,
    parse: false,
  });

export default {
  checkStatus,
  restoreRevision,
  getPostData,
  getRevisions,
};
