/**
 * Transform an array of objects into an object by the specified key and
 * preserve the orignal order with a seperate array
 * @param {Array} array - Array of objects to transform
 * @param {string} key - the key to transform the objects by.
 * @returns {Array} an array containing an object of items and an array of keys
 */
const transformArrayByKey = (array = [], key) =>
  array.reduce(
    ([values = {}, order = []], value) => [
      { ...values, [value[key]]: value },
      [...order, value[key]],
    ],
    [{}, []],
  );

export default transformArrayByKey;
