import PropTypes from 'prop-types';
import classNames from 'classnames';

import '../blocks';
import { STORE_NAME } from '../settings';
import { string, usePrevious, parse } from '../utils';

import Header from '../components/Header';
import Sidebar from '../components/Sidebar';
import RevisionView from './RevisionView';
import RevisionSingleView from './RevisionSingleView';
import NoticeView from './NoticeView';

const { useEffect } = wp.element;
const { withSelect } = wp.data;
const { compose } = wp.compose;

/**
 * Main application container.
 * @param {object} props - component props
 * @param {boolean} props.isInitialised - is app isInitialised
 * @param {boolean} props.postExists - does the post exist
 * @param {number} props.currentRevision - current revision id
 * @param {boolean} props.isRestoring - is app restoring.
 * @param {string} props.currentView - is app restoring.
 * @returns {wp.element.Component} App
 */
const App = ({ isInitialised, postExists, currentRevision, isRestoring, currentView }) => {
  const prevState = usePrevious({
    isInitialised,
    currentRevision,
    isRestoring,
  });

  useEffect(() => {
    if (
      currentRevision &&
      (!prevState ||
        prevState.isInitialised !== isInitialised ||
        (prevState.isRestoring !== isRestoring && !isRestoring) ||
        prevState.currentRevision !== currentRevision)
    ) {
      parse(currentRevision);
    }
  });

  if (!isInitialised) {
    return (
      <div className="revisions">
        <NoticeView message={string('editor.messages.initialising')} />
      </div>
    );
  }

  if (!postExists) {
    return (
      <div className="revisions">
        <NoticeView message={string('editor.messages.postNotFound')} />
      </div>
    );
  }

  const className = classNames('revisions', {
    'is-single-view': currentView === 'single',
  });

  return (
    <div className={className}>
      <Header />
      {currentView === 'split' && <RevisionView />}
      {currentView === 'single' && <RevisionSingleView />}
      <Sidebar />
    </div>
  );
};

App.propTypes = {
  isInitialised: PropTypes.bool.isRequired,
  postExists: PropTypes.bool.isRequired,
  currentRevision: PropTypes.string,
  isRestoring: PropTypes.bool.isRequired,
  currentView: PropTypes.string.isRequired,
};

const composeWithSelect = withSelect((select) => {
  const store = select(STORE_NAME);
  return {
    isInitialised: store.isInitialised(),
    postExists: !!store.getPost(),
    currentRevision: store.getCurrentRevision() ?? store.getPost(),
    isRestoring: store.isRestoring(),
    currentView: store.getView(),
  };
});

export default compose(composeWithSelect)(App);
