import api from './api';
import { STORE_NAME } from '../settings';

const { select: sel, dispatch: dis } = wp.data;

/**
 * Fetch the next page of reviews
 */
const paginateRevisions = async () => {
  const select = sel(STORE_NAME);
  const dispatch = dis(STORE_NAME);

  dispatch.setIsPaginating(true);

  const currentPage = select.getCurrentPage();
  const currentPost = select.getPostId();

  const response = await api.getRevisions(
    currentPost,
    currentPage + 1,
    sel('core/editor').getCurrentPostType(),
  );
  const results = await response.json();
  const maxPages = parseInt(response.headers.get('x-wp-totalpages'), 10);

  dispatch.paginateRevisions({ currentPage: currentPage + 1, results, maxPages });
};

export default paginateRevisions;
