import PropTypes from 'prop-types';
import { decode } from 'he';
import classNames from 'classnames';
import { STORE_NAME } from '../settings';
import { parse } from '../utils';

const { compose } = wp.compose;
const { Button, ButtonGroup } = wp.components;
const { withDispatch, withSelect } = wp.data;
const { useEffect, useState } = wp.element;

/**
 * Revisions Header Layout component.
 * @param {object} props component props
 * @param {object} props.post current post
 * @param {Function} props.setView current post
 * @param {Function} props.setCurrentRevision set the current revision
 * @param {Function} props.getCurrentRevision get the current revision
 * @param {Function} props.getRevisionOrder get the list of revisions
 * @param {Function} props.getView get the current view
 * @returns {wp.element.Component} Header
 */
const Header = ({
  post,
  setView,
  setCurrentRevision,
  getCurrentRevision,
  getRevisionOrder,
  getView,
}) => {
  const button =
    document.referrer && document.referrer.startsWith(window.location.origin) ? (
      <Button isLink text={decode('&larr; Go Back')} href={document.referrer} />
    ) : null;

  const [viewState, setViewState] = useState(getView());

  useEffect(() => {
    setView(viewState);

    let id = getCurrentRevision();

    if (viewState === 'single' && !id) {
      setCurrentRevision(post.id);
    }

    if (viewState === 'split' && id === post.id) {
      const revisionIds = getRevisionOrder();
      [id] = revisionIds;
      setCurrentRevision(id);
    }

    parse(id);
  }, [viewState]);

  const singleClassNames = classNames({
    'is-primary': viewState === 'single',
  });

  const splitClassNames = classNames({
    'is-primary': viewState === 'split',
  });

  return (
    <header className="revisions-header">
      <div className="revision-header--left-group">
        <h1>Compare revisions of "{decode(post.title.rendered)}"</h1>
        {button}
      </div>
      <div className="revision-header--right-group">
        <ButtonGroup className="revision-view--button-group">
          <Button className={singleClassNames} onClick={() => setViewState('single')}>
            Single View
          </Button>
          <Button className={splitClassNames} onClick={() => setViewState('split')}>
            Split View
          </Button>
        </ButtonGroup>
      </div>
    </header>
  );
};

Header.propTypes = {
  post: PropTypes.object,
  setView: PropTypes.func,
  setCurrentRevision: PropTypes.func,
  getCurrentRevision: PropTypes.func,
  getRevisionOrder: PropTypes.func,
  getView: PropTypes.func,
};

const composeWithSelect = withSelect((select) => {
  const { getPost, getEditLink, getCurrentRevision, getView, getRevisionOrder } =
    select(STORE_NAME);

  return {
    post: getPost(),
    editLink: getEditLink(),
    getCurrentRevision,
    getRevisionOrder,
    getView,
  };
});

const composeWithDispatch = withDispatch((dispatch) => {
  const { setView, setCurrentRevision } = dispatch(STORE_NAME);

  return {
    setView,
    setCurrentRevision,
  };
});

export default compose(composeWithSelect, composeWithDispatch)(Header);
