const { registerBlockType } = wp.blocks;
const { InnerBlocks } = wp.blockEditor;
const { Button, Dashicon, Tooltip } = wp.components;
const { __ } = wp.i18n;

const updatedBlockName = 'newspress/revision-no-support';

/**
 * Force element tooltip to show.
 */
const forceToolTip = () => {
  const element = document.querySelector('.components-popover.components-tooltip');

  if (!element) {
    window.requestAnimationFrame(forceToolTip);
  } else {
    element.classList.add('revisions-enforce-tooltip');
  }
};

const tooltipText = __('Block cannot be compared.', 'newspress-revisions');

registerBlockType(updatedBlockName, {
  title: __('No Support', 'newspress'),
  category: 'common',
  supports: {
    inserter: false,
  },
  /**
   * Edit Component
   * @param {object} props component props
   * @param {string} props.className block className
   * @returns {wp.element.Component} edit
   */
  edit: ({ className }) => (
    <span className={className} data-insert-type="block">
      <Tooltip text={tooltipText} animate={false}>
        <Button className="revision-info-button" onMouseOver={forceToolTip} onFocus={forceToolTip}>
          <Dashicon icon="info" />
        </Button>
      </Tooltip>
      <InnerBlocks />
    </span>
  ),
  /**
   * Return inner blocks
   * @returns {wp.element.Component} Save
   */
  save: () => <InnerBlocks.Content />,
});
