import parseDiff from './parseDiff';

const { createBlock } = wp.blocks;
const { applyFilters } = wp.hooks;

/**
 * Return an array of diffs wrapping changes in the specified tag.
 * @param {string} revision - Revision Title
 * @param {string} current - Current Title
 * @param {string} type - Type of tag to wrap changes in.
 * @returns {Array} diffs
 */
export const mapDiff = async (revision, current, type = 'ins') => {
  const { currentRevision: diff } = await parseDiff(revision, current);

  const mapped = diff.map((item) => {
    const updated = item;

    if (!item.added) {
      updated.added = false;
    } else {
      updated.value = `<${type}>${item.value}</${type}>`;
    }

    if (!item.removed) {
      updated.removed = false;
    }

    return updated;
  });

  return mapped.filter((item) => !item.removed);
};

/**
 * Wrap the block in a revision diff block if changes happened
 * @param {string} string - string to wrap
 * @param {string?} type - type of changes
 * @returns {object} block
 */
const createDiffedBlock = (string, type = null) => {
  let wrapperBlock = null;

  if (['ins', 'del'].includes(type)) {
    wrapperBlock = createBlock(`newspress/revision-${type}`, {});
  }

  const titleBlock = createBlock('newspress/revision-title', {
    title: string,
  });

  if (wrapperBlock) {
    wrapperBlock.innerBlocks = [titleBlock];

    return wrapperBlock;
  }

  return titleBlock;
};

/**
 * Diff two strings titles against eachother.
 * @param {string} revision - revision string
 * @param {string} current - current string
 * @param {boolean} reverseDiffTags - whether to reverse the tags used for diffing - ins/del
 * @returns {Array} [revisionBlock, currentBlock]
 */
export default async (revision, current, reverseDiffTags = false) => {
  const hideCoreTitle = applyFilters('newspress.revisions.hideCoreTitle', false);
  if (hideCoreTitle) {
    return [null, null];
  }

  const revisionDiff = await mapDiff(current, revision, reverseDiffTags ? 'ins' : 'del');
  const currentDiff = await mapDiff(revision, current, reverseDiffTags ? 'del' : 'ins');

  const hasRevisionAdded = revisionDiff.some((item) => item.added);
  const hasCurrentAdded = currentDiff.some((item) => item.added);

  const currentString = currentDiff.map((e) => e.value).join('');
  const currentType = hasCurrentAdded ? (reverseDiffTags ? 'del' : 'ins') : null; // eslint-disable-line no-nested-ternary

  // Create standard title block with no wrapper on current if no revisions.
  const currentBlock = revision
    ? createDiffedBlock(currentString, currentType)
    : createBlock('newspress/revision-title', {
        title: current,
      });

  const revisionString = revisionDiff.map((e) => e.value).join('');
  const revisionType = hasRevisionAdded ? (reverseDiffTags ? 'ins' : 'del') : null; // eslint-disable-line no-nested-ternary
  const revisionBlock = createDiffedBlock(revisionString, revisionType);

  return [revisionBlock, currentBlock];
};
