const actions = {
  /**
   * Initialise revisions store
   * @param {object} payload - initial store data.
   * @returns {object} redux action.
   */
  setup: (payload) => ({
    type: 'SETUP',
    payload,
  }),
  /**
   * Set parsing for specific revision to value
   * @param {number} key - revision id
   * @param {boolean} value - is the revision parsing
   * @returns {object} redux action.
   */
  setParsing: (key, value) => ({
    type: 'SET_PARSING',
    payload: {
      key,
      value,
    },
  }),
  /**
   * Set blocks for revision
   * @param {number} key - revision id
   * @param {Array} value - comparision blocks
   * @returns {object} redux action.
   */
  setBlocks: (key, value) => ({
    type: 'SET_BLOCKS',
    payload: {
      key,
      value,
    },
  }),
  /**
   * Set current revision
   * @param {object} payload - revision id
   * @returns {object} redux action.
   */
  setCurrentRevision: (payload) => ({
    type: 'SET_CURRENT_REVISION',
    payload,
  }),
  /**
   * Set revision permissions status
   * @param {object} payload - revision permissions
   * @returns {object} redux action.
   */
  setStatus: (payload) => ({
    type: 'SET_STATUS',
    payload,
  }),
  /**
   * Set restoring
   * @param {boolean} payload - is restoring.
   * @returns {object} redux action.
   */
  setRestoring: (payload) => ({
    type: 'SET_RESTORING_STATUS',
    payload,
  }),
  /**
   * Set post object
   * @param {object} payload - Post object
   * @returns {object} redux action.
   */
  setPost: (payload) => ({
    type: 'SET_POST',
    payload,
  }),
  /**
   * Set is paginating.
   * @param {boolean} payload - is paginating
   * @returns {object} redux action.
   */
  setIsPaginating: (payload) => ({
    type: 'SET_IS_PAGINATING',
    payload,
  }),
  /**
   * Add paginated revisions to store
   * @param {object} payload - paginated revisions data.
   * @returns {object} redux action.
   */
  paginateRevisions: (payload) => ({
    type: 'PAGINATE_REVISIONS',
    payload,
  }),
  /**
   * Set blocks meta fields.
   * @param {string} name - block name
   * @param {Array} fields - block meta fields
   * @returns {object} redux action.
   */
  setBlockMetaFields: (name, fields) => ({
    type: 'SET_BLOCK_META',
    payload: {
      name,
      fields,
    },
  }),
  /**
   * Filters array of revisions by publish types.
   * @param {string} payload - type of publish state to filter by.
   * @returns {object} redux action.
   */
  filterRevisionsByState: (payload) => ({
    type: 'FILTER_REVISIONS_BY_STATE',
    payload,
  }),
  /**
   * Set the current view of revisions.
   * @param {string} payload - view.
   * @returns {object} redux action.
   */
  setView: (payload) => ({
    type: 'SET_REVISIONS_VIEW',
    payload,
  }),
};

export default actions;
