import { blockExists } from '../utils';
import { mapDiff } from '../parser/CreateTitleDiff';

const selectors = {
  /**
   * Return initialising status from state.
   * @param {object} state - current state
   * @param {boolean} state.initialising - is initialising
   * @returns {boolean} is initialising
   */
  isInitialised: ({ initialising }) => !initialising,
  /**
   * Return current post id from state.
   * @param {object} state - current state
   * @param {number} state.postId - current post id
   * @returns {number} post id.
   */
  getPostId: ({ postId }) => postId,
  /**
   * Return post object
   * @param {object} state - current state
   * @param {object} state.post - post object
   * @param {string|number} state.currentRevision - Current revision key
   * @param {object} state.revisions - post revisions
   * @param {boolean} mostRecent - whether to return the most recent post or the right revision comparison
   * @returns {object} post object.
   */
  getPost: ({ post, currentRevision, revisions }, mostRecent = false) => {
    if (!currentRevision || currentRevision === -1 || mostRecent) {
      return post;
    }

    const [, revisionKey] = currentRevision.split('-');

    if (revisionKey === 'post' || revisionKey === post.id.toString()) {
      return post;
    }

    return revisions[revisionKey];
  },
  /**
   * Get an array of id's that represent revisions in order.
   * @param {object} state - current state
   * @param {Array} state.revisionsOrder - Revisions order array
   * @returns {Array} Revisions order array
   */
  getRevisionOrder: ({ revisionsOrder }) => revisionsOrder,
  /**
   * Get revisions
   * @param {object} state - current state
   * @param {object} state.revisions - post revisions
   * @returns {object} post revisions
   */
  getRevisions: ({ revisions }) => revisions,
  /**
   * Get revision by revision ID
   * @param {object} state - current state
   * @param {object} state.revisions - post revisions
   * @param {number} id - Revision ID
   * @returns {object} Revision
   */
  getRevision: ({ revisions }, id) => revisions[id],
  /**
   * Get current revision ID
   * @param {object} state - current state
   * @param {number} state.currentRevision - current revision id
   * @returns {number} revision id.
   */
  getCurrentRevision: ({ currentRevision }) => currentRevision,
  /**
   * Get current revision object
   * @param {object} state - current state
   * @param {number} state.currentRevision - current revision id.
   * @param {Array} state.revisions - post revisions
   * @param {object} state.post - the current post.
   * @returns {object} current post revision
   */
  getCurrentRevisionObject: ({ currentRevision, revisions, post }) => {
    if (currentRevision === -1) {
      return undefined;
    }

    const [revision] = currentRevision.split('-');

    if (revision === 'post' || revision === post.id.toString()) {
      return post;
    }

    return revisions[revision];
  },
  /**
   * Get revisions blocks from the split view.
   * @deprecated since version 3.0
   * @param {object} state - current state
   * @param {object} state.blocks - block storage of parsed revision blocks.
   * @param {number} key - revision id
   * @returns {Array} returns an array of parsed revisions blocks against the current post.
   */
  getBlocks: ({ blocks }, key) => {
    wp.deprecated('getBlocks()', {
      alternative: 'getCachedBlocks()',
      plugin: 'ncu-newspress-revisions',
      since: '3.0.0',
    });
    return blocks?.split?.[key] || false;
  },
  /**
   * Get revisions blocks that have been cached.
   * @param {object} state - current state
   * @param {object} state.blocks - block storage of parsed revision blocks.
   * @param {object} state.view - the current view of revisions.
   * @param {number} key - revision id
   * @returns {Array} returns an array of parsed revisions blocks against the current post.
   */
  getCachedBlocks: ({ blocks, view }, key) => blocks?.[view]?.[key] || false,
  /**
   * Returns if the blocks for the passed revisions id is parsing
   * @param {object} state - current state
   * @param {object} state.parsing - array of parsing status's
   * @param {number} key - Revision ID.
   * @returns {boolean} if revisions blocks are parsing.
   */
  isParsing: ({ parsing }, key) => parsing?.[key] || false,
  /**
   * Returns if the user has permission to restore a revision
   * @param {object} state - current state
   * @param {object} state.status - current post permissions
   * @returns {boolean} can user restore post.
   */
  canRestoreRevision: ({ status }) => status.canRestore,
  /**
   * Returns if the current post is locked.
   * @param {object} state - current state
   * @param {object} state.status - current post permissions
   * @returns {boolean} is post locked.
   */
  isPostLocked: ({ status }) => status.isLocked,
  /**
   * Returns if the revisions is open in readonly mode.
   * @param {object} state - current state
   * @param {object} state.status - current post permissions
   * @returns {boolean} is revisions readonly.
   */
  readonly: ({ status }) => status.readonly || false,
  /**
   * Is revisions currently restoring a revision
   * @param {object} state - current state
   * @param {boolean} state.restoring - is revisions restoring
   * @returns {boolean} is revisions restoring.
   */
  isRestoring: ({ restoring }) => restoring,
  /**
   * Is currently paginating
   * @param {object} state - current state
   * @param {boolean} state.isPaginating - is currently paginating
   * @returns {boolean} is currently paginating
   */
  isPaginating: ({ isPaginating }) => isPaginating,
  /**
   * Return the current page of revisions
   * @param {object} state - current state
   * @param {number} state.currentPage - current page
   * @returns {number} current page.
   */
  getCurrentPage: ({ currentPage }) => currentPage,
  /**
   * Returns the max number of pages that can be paginated.
   * @param {object} state - current state
   * @param {number} state.maxPages - maximum number of pages
   * @returns {number} maximum number of pages
   */
  getMaxPages: ({ maxPages }) => maxPages,
  /**
   * Get post edit link
   * @param {object} state - current state
   * @param {string} state.editLink - post edit link
   * @returns {string} post edit link
   */
  getEditLink: ({ editLink }) => editLink,

  /**
   * Check whether the given block is in the revision set.
   * @param {object} state - current state,
   * @param {object} state.blocks - the list of current existing blocks.
   * @param {object} state.currentRevision - the current revision set.
   * @param {object} state.view - the current revision view.
   * @param {string} clientId - the id of the block to check for.
   * @returns {boolean} whether the given block exists.
   */
  isBlockInRevision: ({ blocks, currentRevision, view }, clientId) =>
    blockExists(blocks[view][currentRevision][0], clientId),

  /**
   * Get post attribute from block id. Checks whether the block is a revision or main post
   * @param {object} state - current state
   * @param {string} state.blocks - The cached blocks.
   * @param {string} state.view - The view that revisions is set to.
   * @param {string} state.currentRevision - The view that revisions is set to.
   * @param {string} state.revisions - The list of revisions.
   * @param {string} state.post - The current post.
   * @param {string} blockId - block id.
   * @param {string} key - post attribute name.
   * @returns {*} post attribute
   */
  getPostFromBlockId: ({ blocks, view, currentRevision, revisions, post }, blockId, key) => {
    const isBlockInRevision = blockExists(blocks[view][currentRevision][0], blockId);

    if (view === 'single' && post.id === currentRevision) {
      return post?.[key];
    }

    if (isBlockInRevision) {
      return revisions?.[currentRevision]?.[key];
    }

    return post?.[key];
  },
  /**
   * Returns true as if we are on this page revisions is open.
   * @returns {boolean} always returns true
   */
  isOpen: () => true,
  /**
   * Get block meta fields
   * @param {object} state - current state
   * @param {object} state.blockMeta - block meta
   * @param {string} name - name of block meta string
   * @returns {*} Block meta value
   */
  getBlockMetaFields: ({ blockMeta }, name) => blockMeta?.[name] || false,
  /**
   * Get revisions counts.
   * @param {object} state - current state
   * @param {object} state.counts - event status counts
   * @returns {object} object of post statuses with counts
   */
  getCounts: ({ counts }) => counts || {},
  /**
   * Get whether to show input allowing switching the right hand side post.
   * @param {object} state - current state
   * @param {number} state.showInput - number of drafts
   * @returns {number} draft count
   */
  getShowInput: ({ showInput }) => showInput || false,
  /**
   * Get index of right hand side post selection.
   * @param {object} state - current state
   * @param {number} state.parentIndex - position of right hand side post index
   * @returns {number} index selection
   */
  getParentIndex: ({ parentIndex }) => parentIndex || 0,
  /**
   * Get index of left hand side post selection.
   * @param {object} state - current state
   * @param {number} state.revisionIndex - position of left hand side post index
   * @returns {number} index selection
   */
  getRevisionIndex: ({ revisionIndex }) => revisionIndex || 0,
  /**
   * Get filter options for filter select control.
   * @param {object} state - Current state.
   * @param {Array} state.filterOptions - Post event state options.
   * @returns {Array} - Select control values.
   */
  getFilterOptions: ({ filterOptions }) => filterOptions || [],
  /**
   * Get timeline filter state value.
   * @param {object} state - current state
   * @param {number} state.stateFilter - filter value
   * @returns {string} filter calue
   */
  getFilterState: ({ stateFilter }) => stateFilter || '',

  /**
   * Gets the current active view of revisions.
   * @param {object} state - The current state.
   * @param {string} state.view - The view that revisions is set to.
   * @returns {string} The current active view.
   */
  getView: ({ view }) => view || 'split',

  /**
   * Get the diffing of two different strings.
   * @param {object} state - current state
   * @param {string} revisions - the revision to use in comparison
   * @param {string} current - the current post to use in comparison
   * @param {string} comparisonType - the type of comparison, ins or del
   * @returns {Array} array of diffed items
   */
  getStringDiff: (state, revisions, current, comparisonType) => {
    const mapped = mapDiff(revisions, current, comparisonType);

    return mapped.then((diff) => diff.map((items) => items.value).join(''));
  },
};

export default selectors;
