/**
 * Return plugin strings from string object.
 * @param {string} key - String key
 * @param {string} def - default value if it is not present in the table
 * @returns {string|object} a string or object dependant on the key passed
 */
const string = (key, def = '') => {
  let value = window.RevisionsView?.strings;
  const identifiers = key.split('.');

  for (let i = 0; i < identifiers.length; i += 1) {
    const identifier = identifiers[i];

    if (!value?.[identifier]) {
      return def;
    }

    value = value[identifier];
  }

  return value;
};

export default string;
