/**
 * Produces the regulat expression for checking the allowed HTML
 * tags and their placeholder replacement during html to text
 * conversion for the parse.
 *
 * <*> - Standard html.
 * {{*}} - Placeholder replacement html.
 * @returns {RegExp} allowed html regular expression
 */
export default () => new RegExp('(?:<|{x-html{)([^!].*?)(?:>|}x-html})', 'g'); // eslint-disable-line
