import PropTypes from 'prop-types';
import { STORE_NAME } from '../settings';
import { paginateRevisions, string } from '../utils';
import Item from './TimelineItem';

const { withSelect, withDispatch } = wp.data;
const { compose } = wp.compose;
const { Fragment } = wp.element;
const { Button, SelectControl } = wp.components;
const { applyFilters } = wp.hooks;

/**
 * Timeline Component
 * @param {object} props Block props
 * @param {object} props.revisions all revisions
 * @param {Array} props.revisionsOrder order of revisions to display
 * @param {number} props.currentRevision current revision id
 * @param {Function} props.setCurrentRevision set active revision
 * @param {number} props.currentPage current page
 * @param {number} props.maxPages max number of pages
 * @param {boolean} props.isPaginating currently paginating
 * @param {object} props.post post data
 * @param {Function} props.filterRevisionsByState filters the timeline by post status
 * @param {string} props.stateFilter post status timeline filter
 * @param {Array} props.filterOptions newspress_status options for filtering list
 * @param {number} props.parentIndex current index of the top selection (right hand post)
 * @param {string} props.view the current view of revisions
 * @returns {wp.element.Component} Timeline
 */
const Timeline = ({
  revisions,
  revisionsOrder,
  currentRevision,
  setCurrentRevision,
  currentPage,
  maxPages,
  isPaginating,
  post,
  filterRevisionsByState,
  stateFilter,
  filterOptions,
  parentIndex,
  view,
}) => {
  const years = [];

  /**
   * Whether or not to print the article edit date
   * Each group of edits are grouped by the year they were edited.
   * @param {string} date - Post date string as provided by WP.
   * @returns {string} - HTML element to print.
   */
  const shouldPrintDate = (date) => {
    const postDate = new Date(date).getFullYear();
    const currentYear = new Date().getFullYear();

    if (postDate === currentYear) {
      return null;
    }

    if (!years.includes(postDate)) {
      years.push(postDate);

      return <h4>{postDate}</h4>;
    }

    return null;
  };

  return (
    <>
      {/* Filter controls by post publish state, defaults to no filter, only render if more than 1 type */}
      {filterOptions.length > 2 && (
        <SelectControl
          label={string('editor.revision.status_messages.filter_label')}
          className="revision-filter"
          value={stateFilter}
          options={applyFilters('newspress.revisions.filterOptions', filterOptions)}
          onChange={(filter) => {
            const value = filter.split(',');
            filterRevisionsByState(value);
          }}
        />
      )}

      <div className="revisionsTimeline">
        <ul className="revisionsTimeline-list">
          {shouldPrintDate(post.modified)}
          <Item
            view={view}
            onClick={view === 'split' ? null : setCurrentRevision(post.id)}
            status={post.newspress_status || post.status}
            date={post.modified}
            name={post?.author_meta?.displayname || ''}
            post={post}
            statusCount={parseInt(post.newspress_count, 10)}
            id={post.id}
            isSelected={
              view === 'single' && parseInt(currentRevision.split('-')[0], 10) !== post.id
                ? revisionsOrder.length === 0 || false
                : currentRevision.endsWith('-post')
            }
            parentIndex={parentIndex}
            additional={{
              savedBy: post.authorData,
            }}
            isMostRecent
            revisions={revisions}
          />
          {revisionsOrder.map((id, index) => {
            const revision = revisions[id];

            if (
              stateFilter !== 'all' &&
              stateFilter?.[0] !== 'all' &&
              !stateFilter.includes(revision.newspress_status)
            ) {
              return null;
            }

            return (
              <Fragment key={revision.id}>
                {shouldPrintDate(revision.modified)}
                <Item
                  view={view}
                  key={revision.id}
                  onClick={setCurrentRevision(id, index)}
                  status={revision.newspress_status || revision.status}
                  statusCount={revision.newspress_count}
                  date={revision.date}
                  name={revision?.author_meta?.displayname || ''} // eslint-disable-line camelcase
                  post={revision}
                  id={id}
                  isSelected={currentRevision.split('-').includes(`${id}`)}
                  timelineIndex={index}
                  parentIndex={parentIndex}
                  isParent={parentIndex - 1 === index}
                  additional={{
                    savedBy: revision.authorData, // the current author.
                  }}
                />
              </Fragment>
            );
          })}
        </ul>
      </div>
      {currentPage < maxPages && (
        <div className="revisionsTimeline-paginate">
          <Button
            isPrimary
            isBusy={isPaginating}
            disabled={isPaginating}
            onClick={paginateRevisions}
          >
            {isPaginating ? 'Loading...' : 'Load More'}
          </Button>
        </div>
      )}
    </>
  );
};

Timeline.propTypes = {
  revisions: PropTypes.object,
  revisionsOrder: PropTypes.arrayOf(PropTypes.number),
  currentRevision: PropTypes.string,
  setCurrentRevision: PropTypes.func,
  currentPage: PropTypes.number,
  maxPages: PropTypes.number,
  isPaginating: PropTypes.bool,
  post: PropTypes.object,
  filterRevisionsByState: PropTypes.func,
  stateFilter: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
  filterOptions: PropTypes.arrayOf(PropTypes.object),
  parentIndex: PropTypes.number,
  view: PropTypes.string,
};

const composeWithSelect = withSelect((select) => {
  const store = select(STORE_NAME);

  return {
    currentRevision: store.getCurrentRevision(),
    post: store.getPost(true),
    revisions: store.getRevisions(),
    revisionsOrder: store.getRevisionOrder(),
    currentPage: store.getCurrentPage(),
    isPaginating: store.isPaginating(),
    maxPages: store.getMaxPages(),
    stateFilter: store.getFilterState(),
    filterOptions: store.getFilterOptions(),
    revisionIndex: store.getRevisionIndex(),
    parentIndex: store.getParentIndex(),
    view: store.getView(),
  };
});

const composeWithDispatch = withDispatch((dispatch) => {
  const store = dispatch(STORE_NAME);

  return {
    /**
     * Set current Revision
     * @param {number} id revision id
     * @param {number} index - index of timeline item as it appears in list.
     * @returns {Function} update current revision
     */
    setCurrentRevision: (id, index) => () => store.setCurrentRevision({ id, index }),
    /**
     * Filter timeline by publish state.
     * @param {number} filter Filter value, 'publish', 'draft' or 'all'.
     * @returns {Function} Update current timeline.
     */
    filterRevisionsByState: (filter) => store.filterRevisionsByState(filter),
  };
});

export default compose(composeWithSelect, composeWithDispatch)(Timeline);
