import getUuid from 'uuid-by-string';
import tokenizeDiffs from './tokenizeDiffs';

import allowedHTMLTagsRegExp from './getAllowedTags';
import parseDiff from './parseDiff';
import htmlToText from './htmlToText';
import { blocksNoSupport, blocksNoReplace } from './hooks';
import { compareMeta } from './compareMeta';
import { STORE_NAME } from '../settings';

if (undefined === lodash) {
  lodash = window._;
}

const { parse } = wp.blocks;
const { select } = wp.data;
const { unescape, cloneDeep } = lodash;

const NEWLINE_BLOCK = '\n\n\n\n';
const REGEX_BLOCK_WITH_CONTENT =
  /(?:(?:<!-- wp:((?:(?!revision-).)+?)(?:\s({.+?}))?\s(\/)-->)|(?:<!-- wp:((?:(?!revision-).)+?)(?:\s({.+?}))?\s-->)(?:\n(.[\s\S]+?)\n)<!-- \/wp:\4 -->)/;
const REGEX_BLOCK_NAMES = /<!-- wp:(.+?)(?:\s({.+?}))?\s(?:\/)?-->/;

/**
 * RevisionParser class.
 */
class RevisionParser {
  /**
   * Constructor
   * @param {string} revision - revision content string
   * @param {string} current - latest content string
   * @param {object} options - parsing options
   */
  constructor(revision, current, options = {}) {
    this.options = options;

    const setRevision = this.stripHTML(revision);
    const setCurrent = this.stripHTML(current);

    this.revision = {
      raw: setRevision,
      text: htmlToText(setRevision),
      parsed: setRevision,
      element: options?.reverseDiffTags ? 'ins' : 'del',
    };

    this.current = {
      raw: setCurrent,
      text: htmlToText(setCurrent),
      parsed: setCurrent,
      element: options?.reverseDiffTags ? 'del' : 'ins',
    };

    this.type = 'current';
  }

  /**
   * Return the current revision type.
   * @returns {string} revision type key.
   */
  getType = () => this[this.type];

  /**
   * Return the other type.
   * @returns {string} other type.
   */
  getOtherType = () => {
    let otherType = 'current';

    if (this.type === 'current') {
      otherType = 'revision';
    }

    return this[otherType];
  };

  /**
   * Strips any html from the contentas defined in the stripHTML
   * option assigned in the constructor.
   * @param {string} content Content to strip the HTML from.
   * @returns {string} stripped html
   */
  stripHTML(content = '') {
    if (!this.options.stripHTML) {
      return content;
    }

    const tags = this.options.stripHTML;

    let updatedContent = content;

    tags.forEach((tag) => {
      let attributesString = '';

      if (tag.attributes) {
        const entries = Object.entries(tag.attributes);

        // eslint-disable-next-line no-restricted-syntax
        for (const [attr, value] of entries) {
          attributesString += `${attr}="${value}" `;
        }

        attributesString = attributesString.trim();
      }

      const regex = new RegExp(`<${tag.name}[^>]+${attributesString}.*?>(.+?)</${tag.name}>`, 'g');

      updatedContent = updatedContent.replace(regex, (_match, innerText) => {
        if (tag.removeText) {
          return '';
        }

        return innerText;
      });
    });

    return updatedContent;
  }

  /**
   * Replace the nth paragraph.
   * @param {string} content - the full string
   * @param {number} paragraph - paragraph index to apply callback too
   * @param {Function} callback - callback to apply to the block.
   * @returns {string} revised content.
   */
  replaceNthParagraph = (content, paragraph, callback) => {
    let nth = 0;

    const blockRegex = new RegExp(REGEX_BLOCK_WITH_CONTENT, 'g');

    return content.replace(
      blockRegex,
      (
        block,
        singleBlockName,
        // eslint-disable-next-line no-unused-vars
        attrs = {},
        // eslint-disable-next-line no-unused-vars
        selfClosing = false,
        blockName,
      ) => {
        nth += 1;

        if (blocksNoSupport().includes(singleBlockName || blockName)) {
          return block;
        }

        if (nth === paragraph) {
          return callback(singleBlockName || blockName, block);
        }

        return block;
      },
    );
  };

  /**
   * Get paragraph content at desired index.
   * @param {number} paragraph - paragraph to fetch
   * @param {string} content - content string
   * @returns {string} paragraph content
   */
  getParagraphContent = (paragraph = 0, content) => {
    // Clean up for easier matching.
    const blockMatches = content.match(new RegExp(REGEX_BLOCK_WITH_CONTENT, 'g'));

    if (!blockMatches[paragraph - 1]) {
      return false;
    }

    const block = blockMatches[paragraph - 1];

    const matches = block.match('<!-- wp:.+? -->\n(.+?)\n<!-- /wp:.+? -->');

    if (!matches) {
      return false;
    }

    return matches[1].trim();
  };

  /**
   * is the current string a html tag
   * @param {string} string - string to check
   * @returns {boolean} is string a html tag
   */
  isHTMLTag = (string) => {
    // Matches any HTML element that is its own string.
    const matches = /^(<(\/)?([a-z]+)(\s.+=".+")?>){1,}([,.-])?$/i.exec(string) || [];

    return {
      tag: matches[1] ? matches[1] : '',
      closingTag: matches[3] === '/',
    };
  };

  /**
   * Convert html to text representation
   * @param {string} html - input string
   * @returns {string} filtered/replaced content
   */
  htmlToText = (html = '') => {
    const allowedTagsRegExp = allowedHTMLTagsRegExp();
    const tags = html.replace(allowedTagsRegExp, '{{$1$2$3}}');

    const doc = new DOMParser().parseFromString(tags, 'text/html');
    let value = doc.body.textContent || '';

    value = value.replace(allowedTagsRegExp, '<$1$2$3>');

    // Test for excessive amounts of consecutive new lines and
    // replace them with the defined NEWLINE_BLOCK.
    const regex = new RegExp(/^\n{5,}/, 'g');

    if (regex.test(value)) {
      /* istanbul ignore next */
      value = value.replace(new RegExp(/^\n{5,}/, 'g'), NEWLINE_BLOCK);
    }

    return value;
  };

  /**
   * Join diff items.
   * @returns {Array} diff items
   */
  joinDiffItems() {
    const updatedDiff = [];
    let prevKey = 0;

    this.diff.forEach((item, key) => {
      if (key <= 0) {
        updatedDiff.push(item);
        return;
      }

      const prevAdded = updatedDiff[prevKey].added || false;
      const prevValue = updatedDiff[prevKey].value || '';

      const isHTMLTag = this.isHTMLTag(prevValue);

      if (isHTMLTag.tag && !isHTMLTag.closingTag && prevAdded) {
        updatedDiff[prevKey].value += item.value;

        return;
      }

      // If the item is only punctuation,
      if (/^[.,/#!$%^&*;:{}=\-_`~()]$/.test(item.value)) {
        /* istanbul ignore next */
        const valuePrefix = prevAdded ? updatedDiff[prevKey].value : '';

        /* istanbul ignore next */
        const valueObject = {
          added: true,
          value: valuePrefix + item.value,
        };

        /* istanbul ignore next */
        if (prevAdded) {
          /* istanbul ignore next */
          updatedDiff[prevKey] = valueObject;
        } else {
          /* istanbul ignore next */
          updatedDiff.push(valueObject);
        }
      } else if ((item.added && prevAdded) || item.value === ' ') {
        updatedDiff[prevKey].value += item.value;
      } else {
        updatedDiff.push(item);
      }

      prevKey = updatedDiff.length - 1;
    });

    return updatedDiff;
  }

  /**
   * is content a template block
   * @param {string} content - string content;
   * @returns {boolean} is content a template block
   */
  isTemplatedBlock = (content) => {
    const templatedBlockRegex = new RegExp(/<!-- wp:(.+?)( {.*?})? (\/)?-->/, 'g');
    const matches = content.match(templatedBlockRegex);

    return matches.length > 1;
  };

  /**
   * Cycles through the content and getting attaching the block
   * pointer to each item in the array.
   * @param {Array} diffItems - current diff items
   * @returns {Array} array of content with blocks attached
   */
  addBlockCounts = (diffItems) => {
    let blockCount = 1;
    const preliminaryBlocks = [];

    const { raw: rawContent } = this.getType();

    const singleBlockRegex = new RegExp(REGEX_BLOCK_WITH_CONTENT, 'g');
    const contentBlocks = rawContent.match(singleBlockRegex);

    if (!contentBlocks) {
      return [];
    }

    contentBlocks.forEach((item) => {
      const singleLine = new RegExp(/<!-- wp:(.+?)( {.*?})? (\/)?-->/);
      const contentBlock = item.match(singleLine);

      const values = {
        blockCount,
        value: null,
        added: false,
        removed: false,
        block: {
          name: contentBlock[1],
          attributes: contentBlock[2],
          selfClosing: contentBlock[3] === '/',
          isTemplated: this.isTemplatedBlock(item),
        },
      };

      blockCount += 1;

      preliminaryBlocks.push(values);
    });

    const selfClosing = preliminaryBlocks.filter((b) => b.block.selfClosing);
    const nonSelfClosing = preliminaryBlocks.filter((b) => !b.block.selfClosing);

    let blockIndex = 0;
    const splitBlocks = [];

    diffItems.forEach((diffItem) => {
      const split = diffItem.value.split(/(\n\n\n\n)/g).filter((i) => i !== '');

      split.forEach((splitItem) => {
        if (splitItem === '\n\n\n\n') {
          blockIndex += 1;
          return;
        }

        splitBlocks.push({
          ...nonSelfClosing[blockIndex],
          added: diffItem.added,
          value: splitItem,
        });
      });
    });

    const merged = [];
    let sbIndex = 0;
    let scIndex = 0;
    let current = 0;

    let expect = 1;

    while (current < splitBlocks.length + selfClosing.length) {
      const { blockCount: sbCount = null } = splitBlocks[sbIndex] || {};
      const { blockCount: scCount = null } = selfClosing[scIndex] || {};

      if (sbCount === expect) {
        merged.push(splitBlocks[sbIndex]);
        sbIndex += 1;
      }

      if (scCount === expect) {
        merged.push(selfClosing[scIndex]);
        scIndex += 1;
      }

      if (
        (splitBlocks[sbIndex] &&
          splitBlocks[sbIndex].blockCount > expect &&
          selfClosing[scIndex] &&
          selfClosing[scIndex].blockCount > expect) ||
        (!selfClosing[scIndex] && splitBlocks[sbIndex] && splitBlocks[sbIndex].blockCount > expect)
      ) {
        expect += 1;
      }

      current += 1;
    }

    return merged;
  };

  /**
   * Join diff items together.
   * @param {object} item - current item
   * @param {string} splitStartParagraph - start of paragraph
   * @param {string} splitMatchParagraph - match to replace with.
   * @returns {string} replaced and combined string.
   */
  replaceAndCombine(item, splitStartParagraph, splitMatchParagraph) {
    const { element } = this.getType();

    let newString = splitStartParagraph;
    let itemValue = unescape(item.value);

    if (!splitMatchParagraph.includes(itemValue)) {
      itemValue = itemValue.trim();
    }

    newString += unescape(splitMatchParagraph).replace(
      itemValue,
      `<${element}>${item.value}</${element}>`,
    );

    return newString;
  }

  /**
   * Create Diff elements from diff
   * @param {Array} diffBlocks - diffed blocks
   * @returns {Array} - Gutenberg block representation of changes
   */
  createDiffElements(diffBlocks) {
    const { raw: originalParsedContent } = this.getType();

    const noReplace = blocksNoReplace();

    // Clone the new blocks to ensure immutability.
    const newDiffBlocks = cloneDeep(diffBlocks);
    const tokensToBlocks = newDiffBlocks
      .reduce((carry, item) => {
        const newCarry = carry;
        if (!newCarry[item.blockCount]) {
          newCarry[item.blockCount] = [];
        }

        newCarry[item.blockCount].push(item);
        return newCarry;
      }, [])
      .map((tokens) =>
        tokens.filter((item) => {
          if (!item.value || item.removed) {
            return false;
          }

          return true;
        }),
      );
    const { element } = this.getType();
    const blockRegex = new RegExp(REGEX_BLOCK_WITH_CONTENT, 'g');
    const blocks = [...originalParsedContent.matchAll(blockRegex)];

    return tokensToBlocks
      .map((block, index) => {
        const [blockContent, selfClosingBlockName, , , blockName, attrs = ''] = blocks[index - 1];
        let added = false;

        if (block.length === 0) {
          return {
            added,
            blockName: selfClosingBlockName,
            attrs,
            content: blockContent,
          };
        }

        const childBlockRegex = new RegExp(
          `<!-- wp:${blockName.replace(
            /[-/\\^$*+?.()|[\]{}]/g,
            '\\$&',
          )} -->.*?<!-- ([^\\/]+)\\/? -->`,
          'gs',
        );

        const hasChildBlocks = childBlockRegex.exec(blockContent);

        if (blocksNoSupport().includes(blockName) || hasChildBlocks) {
          return {
            added,
            blockName,
            attrs,
            content: blockContent,
          };
        }

        const innerContent = block.reduce((carry, token) => {
          if (token.isTag || !token.added || !token.value || noReplace.includes(blockName)) {
            return `${carry}${token.value}`;
          }

          added = true;

          return `${carry}<${element}>${token.value}</${element}>`;
        }, '');

        const content = `<!-- wp:${blockName}${
          attrs ? ` ${attrs}` : ''
        } -->\n${innerContent}\n<!-- /wp:${blockName} -->`;

        return {
          added,
          blockName,
          attrs,
          content,
        };
      })
      .map((block) => block.content)
      .join('\n\n');
  }

  /**
   * Wrap block in appropriate revisions block template
   * @param {string} block - block name
   * @param {string} content - block content
   * @returns {string} - wrapped block content
   */
  replacementBlock = (block, content) => {
    let { element } = this.getType();

    if (blocksNoSupport().includes(block)) {
      element = 'no-support';
    }

    return `<!-- wp:newspress/revision-${element} -->\n${content}\n<!-- /wp:newspress/revision-${element} -->`;
  };

  /**
   * Wrap content in an updated block.
   * @param {string} block - block name
   * @param {string} content - block content
   * @returns {string} wrapped content
   */
  updatedBlock = (block, content) =>
    `<!-- wp:newspress/revision-updated -->\n${content}\n<!-- /wp:newspress/revision-updated -->`;

  /**
   * Create diff blocks.
   * @param {string} diffBlocks - content as blocks
   * @param {Array} diff - array of diffs
   * @returns {string} diffed blocks
   */
  createDiffBlocks(diffBlocks = '', diff) {
    const spacesRegex = /&nbsp;/g;
    let parsedContent = diffBlocks.replace(spacesRegex, '\u00A0');

    const blockRegex = new RegExp(REGEX_BLOCK_WITH_CONTENT, 'g');

    // @TODO: Replace filter with own function and remove single character vars.
    const paragraphBlocks = parsedContent.match(blockRegex);

    if (!paragraphBlocks) {
      return parsedContent;
    }

    const noSupport = blocksNoSupport();
    const noReplace = blocksNoReplace();

    const { raw: unprocessedRaw, diffs: otherDiffs } = this.getOtherType();
    const raw = unprocessedRaw.replace(spacesRegex, '\u00A0');
    const blockContentRegex = new RegExp(REGEX_BLOCK_WITH_CONTENT, 'g');

    const comparisonDiff = raw.match(blockContentRegex);

    paragraphBlocks.forEach((paragraph, key) => {
      // maintain order as the order could have changed and this will technically be a change.
      const blockNumber = key + 1;

      const hasNotChanged = comparisonDiff?.some(
        (item) => getUuid(item || '', 5) === getUuid(paragraph || '', 5),
      );

      const block = diff.find((item) => item.blockCount === blockNumber) || {};

      const { block: { name: blockType = '', attributes: blockAttributes = '' } = {} } = block;

      if (noSupport.includes(blockType) || block.block?.isTemplated) {
        parsedContent = this.replaceNthParagraph(parsedContent, key + 1, this.replacementBlock);
        return;
      }

      if (hasNotChanged && !select(STORE_NAME).getBlockMetaFields(blockType)) {
        return;
      }

      const removeAttributesFromNameRegex = /(.*?)(\s{.*)/;

      const isOld = otherDiffs.some(
        (item) => item.block.name === blockType && item.blockCount === blockNumber,
      );

      /**
       * Check if the block is added.
       * @param {item} item - item
       * @returns {boolean} has block been added
       */
      const isAddedCallback = (item) =>
        item.blockCount === blockNumber &&
        item.added &&
        !item.isTag &&
        item.block.name.replace(removeAttributesFromNameRegex, '$1') ===
          blockType.replace(removeAttributesFromNameRegex, '$1');

      /**
       * Has the block changed.
       * @param {object} item - block
       * @returns {boolean} is changed
       */
      const isChangedCallback = (item) =>
        item.blockCount === blockNumber &&
        item.removed &&
        item.block.name.replace(removeAttributesFromNameRegex, '$1') ===
          blockType.replace(removeAttributesFromNameRegex, '$1');

      /**
       * Compare item meta
       * @param {object} item - block
       * @returns {boolean} is meta different
       */
      const isMetaDiffCallback = (item) => compareMeta(item.block.name);

      const { block: { name: oldBlock = false, attributes: oldBlockAttributes = '' } = {} } =
        otherDiffs.find(({ blockCount }) => blockCount === blockNumber) || {};

      const hasAdditions =
        diff.some(isAddedCallback) ||
        otherDiffs.some(isAddedCallback) ||
        diff.some(isMetaDiffCallback) ||
        otherDiffs.some(isMetaDiffCallback);

      let hasDifferentAttributes = oldBlockAttributes !== blockAttributes;

      if (oldBlockAttributes === '') {
        hasDifferentAttributes = false;
      }

      const hasChanges =
        hasDifferentAttributes ||
        diff.some(isChangedCallback) ||
        otherDiffs.some(isChangedCallback);

      let blockCallback = this.replacementBlock;

      /**
       * This should only apply for blocks where
       * unknown changes occur and will ensure that the
       * block-level highlight will always be set to 'updated'.
       */
      if (oldBlock === blockType && !hasDifferentAttributes && noReplace.includes(blockType)) {
        blockCallback = this.updatedBlock;
      }

      if (hasChanges || hasAdditions || !isOld) {
        parsedContent = this.replaceNthParagraph(
          parsedContent,
          key + 1,
          hasAdditions || !isOld || (isOld && !hasDifferentAttributes)
            ? blockCallback
            : this.updatedBlock,
        );
        return;
      }

      // eslint-disable-next-line max-len
      const isSplitBlock = comparisonDiff.some((item) => {
        const itemValue = htmlToText(item).trim();
        const blockValue = htmlToText(paragraph).trim();

        if (itemValue === '' || blockValue === '') {
          return false;
        }

        if (itemValue === blockValue) {
          return false;
        }

        const matches = itemValue.split(blockValue);
        return matches.length > 1;
      });

      if (isSplitBlock) {
        parsedContent = this.replaceNthParagraph(parsedContent, key + 1, this.replacementBlock);
      }
    });

    // Parse and retrieve all block names, including revisions blocks.
    const fullBlockRegex = new RegExp(REGEX_BLOCK_NAMES, 'g');
    const parsedBlocks = [...parsedContent.matchAll(fullBlockRegex)];

    /**
     * Check blocks that are specific to the revisions plugin.
     * @param {string} blockName - The name of the block to check.
     * @returns {boolean} - Whether the block is specific to revisions.
     */
    const isBlockNameRevisions = (blockName) =>
      blockName.startsWith('newspress/revision') || blockName.startsWith('revisions/');

    /**
     * Parse remaining blocks that have not been assigned a revisions block
     * to allow them to be wrapped in the revisions variable block.
     */
    parsedBlocks.forEach((block, key) => {
      // Don't wrap blocks that have already been processed.
      if (isBlockNameRevisions(block[1])) {
        return;
      }

      parsedContent = this.replaceNthParagraph(parsedContent, key + 1, (blockName, content) => {
        // Double check elements that sneak through.
        if (isBlockNameRevisions(blockName)) {
          return content;
        }

        return `<!-- wp:newspress/revision-variable -->\n${content}\n<!-- /wp:newspress/revision-variable -->`;
      });
    });

    return parsedContent;
  }

  /**
   * Get the diff corer the current type.
   * @returns {RevisionParser} return self
   */
  getDiff() {
    const updatedDiff = this.initialDiff;
    let parsedContent = this.createDiffElements(updatedDiff);
    parsedContent = this.createDiffBlocks(parsedContent, updatedDiff);

    this[this.type].parsed = parsedContent;

    return this;
  }

  /**
   * Apply diffs to the parsed content
   */
  applyDiffsToParsed = async () => {
    const { currentRevision, pastRevision } = await parseDiff(
      this.revision.text.trim(),
      this.current.text.trim(),
    );
    const currentType = this.type;

    this.type = 'current';

    let updatedCurrentRevision = this.addBlockCounts(currentRevision);
    updatedCurrentRevision = tokenizeDiffs(updatedCurrentRevision);
    this.current.diffs = updatedCurrentRevision;

    this.type = 'revision';

    let updatedPreviousRevision = pastRevision.map((rev) => ({
      ...rev,
      added: rev.removed,
      removed: false,
    }));
    updatedPreviousRevision = this.addBlockCounts(updatedPreviousRevision);
    updatedPreviousRevision = tokenizeDiffs(updatedPreviousRevision);
    this.revision.diffs = updatedPreviousRevision;

    this.type = currentType;
  };

  /**
   * Get current changes
   * @returns {RevisionParser} current changes
   */
  getCurrent() {
    this.type = 'current';
    this.initialDiff = this[this.type].diffs;
    return this.getDiff();
  }

  /**
   * Get previous contents changes
   * @returns {RevisionParser} previous changes
   */
  getPrevious() {
    this.type = 'revision';
    this.initialDiff = this[this.type].diffs;
    return this.getDiff();
  }

  /**
   * Return parsed string as an array of blocks
   * @returns {Array} array of blocks
   */
  toBlocks() {
    const { parsed } = this.getType();
    return parse(parsed);
  }

  /**
   * Return parsed content as a string
   * @returns {string} parsed content
   */
  toString() {
    const { parsed } = this.getType();
    // Replace escaped characters with unescaped.
    return parsed.replace(/\\([.+*()])/g, '$1');
  }
}

export default RevisionParser;
