import PropTypes from 'prop-types';

const { Button } = wp.components;
const { useState } = wp.element;

/**
 * ToastView is a dismissable notice.
 * @param {object} props - component props
 * @param {string} props.message - String to show in notice.
 * @param {boolean} props.dismissable - is the notice dismissable
 * @returns {wp.element.Component} ToastView
 */
const ToastView = ({ message, dismissable }) => {
  const [showToast, setShowToast] = useState(true);

  return (
    <>
      {showToast && (
        <div className="revisions-message-toast">
          <p>{message}</p>
          {dismissable && <Button icon="dismiss" onClick={() => setShowToast(false)} />}
        </div>
      )}
    </>
  );
};

ToastView.propTypes = {
  message: PropTypes.string.isRequired,
  dismissable: PropTypes.bool,
};

export default ToastView;
