import { string } from '../utils';

const { registerBlockType } = wp.blocks;
const { __ } = wp.i18n;

const noRevisionBlockName = 'newspress/no-revisions';

registerBlockType(noRevisionBlockName, {
  title: __('No Revisions', 'newspress'),
  category: 'common',
  supports: {
    inserter: false,
  },
  /**
   * Return no revisions block
   * @returns {wp.element.Component} No Revisions Block
   */
  edit: () => <div className="content">{string('editor.blocks.no_revisions')}</div>,
  /**
   * Save Component
   * @returns {null} save
   */
  save: () => null,
});
