const { registerBlockType } = wp.blocks;
const { InnerBlocks } = wp.blockEditor;
const { __ } = wp.i18n;

const updatedBlockName = 'newspress/revision-updated';

registerBlockType(updatedBlockName, {
  title: __('Revision Updated', 'newspress'),
  category: 'common',
  supports: {
    inserter: false,
  },
  /**
   * Wrap inner blocks in span tag
   * @param {object} props object props
   * @param {string} props.className className
   * @returns {wp.element.Component} Edit
   */
  edit: ({ className }) => (
    <span className={className} data-insert-type="block">
      <InnerBlocks />
    </span>
  ),
  /**
   * Render InnerBlocks
   * @returns {wp.components.Component} Save
   */
  save: () => <InnerBlocks.Content />,
});
