const { applyFilters } = wp.hooks;

const REGEX_EMPTY_PARAGRAPH =
  /(?:\n)?<!-- wp:paragraph -->\n<p><\/p>\n<!-- \/wp:paragraph -->(?:\n)?/;

/**
 * Strip empty paragraphs from content
 * @param {string} content - string to remove paragraphs from
 * @returns {string} tidied string.
 */
const stripEmptyParagraphs = (content) => {
  const regex = new RegExp(REGEX_EMPTY_PARAGRAPH, 'g');
  return content.replace(regex, '');
};

/**
 * Set content body
 * @param {string} content - content to replace
 * @param {string} replacement - replacement value
 * @returns {string} replaced content.
 */
export const setContentBody = (content = '', replacement = '') => {
  const REGEX_CONTENT_BODY = applyFilters('newspress.revisions.getContentBodyRegex', false);

  if (!REGEX_CONTENT_BODY) {
    return replacement;
  }

  const regexReplace = new RegExp(REGEX_CONTENT_BODY, 'g');
  return content.replace(regexReplace, (body, boundry) => body.replace(boundry, replacement));
};

/**
 * Get the content body.
 * @param {string} content - string content
 * @returns {string} body content
 */
export default (content = '') => {
  const REGEX_CONTENT_BODY = applyFilters('newspress.revisions.getContentBodyRegex', false);

  if (!REGEX_CONTENT_BODY) {
    return content;
  }

  const bodyRegex = new RegExp(REGEX_CONTENT_BODY, 'g');
  const bodyContent = bodyRegex.exec(content);

  if (!bodyContent) {
    return content;
  }

  return stripEmptyParagraphs(bodyContent[1]);
};
