const { applyFilters } = wp.hooks;
const { Dashicon } = wp.components;

/**
 * Get revision icon based on status.
 * @param {string} status - post status
 * @param {object} post - post object
 * @returns {wp.element.Component} Status icon
 */
export default (status, post) => {
  let className = '';

  // eslint-disable-next-line camelcase
  // eslint-disable-next-line react/destructuring-assignment
  if ((status === 'publish' || status === 'update') && !post.saveData?.save_transitioned) {
    className = 'revision--publish-updated';
  }

  return applyFilters(
    'newspress.revisions.getStatusIcon',
    status === 'publish' || status === 'update' ? (
      <Dashicon icon="yes-alt" className={className} />
    ) : (
      false
    ),
    status,
    post,
    className,
  );
};
